// Import utilities
import { itemsObject } from '@builder/common/utilities';
import actionsWithoutConditions from '@builder/item/button-conditions/utilities/get-actions-without-conditions';

/**
 * Checks whether actions should be sortable
 *
 * @return {Boolean} True if they should be
 */
const shouldBeSortable = () => {
	// Default response
	let response = false;

	// Get all actions
	const actions = itemsObject( 'get', {
		filter: {
			itemType: 'action',
		},
	} );

	// Check if there is more than one action
	if ( actions.length > 1 ) {
		// It should be sortable
		response = true;
	} else {
		// Get actions without conditions
		const topLevelActions = actionsWithoutConditions();

		// Get conditions
		const conditions = UncannyAutomator.recipe.conditions;

		// Check if there is more than one item in the top level
		if ( Object.keys( topLevelActions ).length + Object.keys( conditions ).length > 1 ) {
			// It should be sortable
			response = true;
		}
	}

	return response;
};

export default shouldBeSortable;
