// Import utilities and components
import { isDefined } from '@builder/common/utilities';
import getConditionById from '@builder/item/button-conditions/utilities/get-condition-by-id';
import getConditionsGroupById from '@builder/item/button-conditions/utilities/get-conditions-group-by-id';
import updateRemoteConditionsMeta from '@builder/item/button-conditions/utilities/update-remote-conditions-meta';
import getIntegrationIconURL from '@builder/integrations/utilities/get-integration-icon-url';
import getIntegrationByCode from '@builder/integrations/utilities/get-integration-by-code';
import canManageConditions from '@builder/item/button-conditions/utilities/can-manage-conditions';

/**
 * Creates the condition pill used in ConditionsBlock
 */
export default class ConditionPill {
	/**
	 * Creates the pill node
	 *
	 * @param {Object} options                   The options
	 * @param {String} options.conditionsGroupId The unique ID of the conditions group
	 * @param {String} options.conditionId       The unique ID of the condition
	 * @return {undefined}
	 */
	constructor( { conditionsGroupId, conditionId } ) {
		// Save the parameters
		this.conditionsGroupId = conditionsGroupId;
		this.conditionId = conditionId;

		// Get the conditions group
		this.conditionsGroup = getConditionsGroupById( {
			conditionsGroupId: conditionsGroupId,
		} );

		// Get the data of the condition
		this.condition = getConditionById( {
			conditionsGroupId: conditionsGroupId,
			conditionId: conditionId,
		} );

		// Create node
		this.#createPill();
	}

	/**
	 * Creates the node of the pill
	 *
	 * @return {undefined}
	 */
	#createPill() {
		// Create wrapper
		const $pill = document.createElement( 'div' );
		$pill.classList.add( 'uap-conditions-pill' );

		// Set some properties that could be used to add UI exceptions using CSS
		$pill.setAttribute( 'data-integration', this.condition.integrationCode );
		$pill.setAttribute( 'data-condition', this.condition.conditionCode );

		// Save it as a property
		this.$pill = $pill;

		// Add content
		$pill.insertAdjacentElement( 'beforeEnd', this.createIntegrationIcon() );
		$pill.insertAdjacentElement( 'beforeEnd', this.createSentence() );

		// Add actions (edit and remove) only if the user can perform the actionS
		if ( canManageConditions() ) {
			$pill.insertAdjacentElement( 'beforeEnd', this.createActionsTooltip() );
		}

		// Handle remove button
		this.#handleRemoveConditionButton();

		// Handle edit button
		this.#handleEditConditionButton();
	}

	/**
	 * Handles clicks to the "Remove" button
	 *
	 * @return {undefined}
	 */
	#handleRemoveConditionButton() {
		// Check if the user can perform this action
		if ( canManageConditions() ) {
			// Listen clicks on the button
			this.$removeButton.addEventListener( 'click', () => {
				// Remove condition
				this.removeCondition();
			} );
		}
	}

	/**
	 * Handles clicks to the "Edit" button
	 *
	 * @return {undefined}
	 */
	#handleEditConditionButton() {
		// Check if the user can perform this action
		if ( canManageConditions() ) {
			// Listen clicks on the button
			this.$editButton.addEventListener( 'click', () => {
				// Edit condition
				this.editCondition();
			} );
		}
	}

	/**
	 * Removes/deletes the condition
	 *  There is not need to render the UI again on this method. Once the request is finished,
	 * updateRemoteConditionsMeta will create an instance of the Actions class to render the UI again.
	 *
	 * @return {undefined}
	 */
	removeCondition() {
		// Remove the condition from the main object
		this.conditionsGroup.removeCondition( {
			conditionId: this.conditionId,
		} );

		// Update the meta
		updateRemoteConditionsMeta( {} );
	}

	/**
	 * Edit the condition
	 *
	 * @return {undefined}
	 */
	editCondition() {
		// Edit condition
		this.conditionsGroup.editCondition( {
			conditionId: this.conditionId,
		} );
	}

	/**
	 * Returns the node of the button used to remove the condition pill
	 *
	 * @return {Node} The node of the button
	 */
	get $removeButton() {
		return this.$pill.querySelector( '.uap-conditions-pill-actions-remove' );
	}

	/**
	 * Returns the node of the button used to edit the condition pill
	 *
	 * @return {Node} The node of the button
	 */
	get $editButton() {
		return this.$pill.querySelector( '.uap-conditions-pill-actions-edit' );
	}

	/**
	 * Creates and RETURNS a node with the integration icon
	 *
	 * @return {Node} The integration icon
	 */
	createIntegrationIcon() {
		// Create sentence
		const $wrapper = document.createElement( 'div' );
		$wrapper.classList.add( 'uap-conditions-pill-logo' );

		// Get integration
		const integration = getIntegrationByCode( {
			integrationCode: this.condition.integrationCode,
		} );

		// Check if the integration is defined
		if ( isDefined( integration ) ) {
			// Get the icon
			const integrationIcon = getIntegrationIconURL( {
				integrationCode: this.condition.integrationCode,
			} );

			// Add a tooltip with the integration name
			$wrapper.setAttribute( 'uap-tooltip', integration.name );

			// Add image
			$wrapper.innerHTML = `
				<img src="${ integrationIcon }">
			`;
		}

		return $wrapper;
	}

	/**
	 * Creates and RETURNS a node with the pill sentence
	 *
	 * @return {Node} The sentence
	 */
	createSentence() {
		// Create sentence
		const $wrapper = document.createElement( 'div' );
		$wrapper.classList.add( 'uap-conditions-pill-sentence' );

		// Get the condition sentence and append it
		$wrapper.appendChild( this.condition.$dynamicSentence );

		return $wrapper;
	}

	/**
	 * Creates and RETURNS a node with the tooltip that contains the actions (delete and edit)
	 *
	 * @return {Node} The tooltip
	 */
	createActionsTooltip() {
		// Create sentence
		const $wrapper = document.createElement( 'div' );
		$wrapper.classList.add( 'uap-conditions-pill-actions' );

		$wrapper.innerHTML = `
			<span class="uap-conditions-pill-actions-edit">
				<automator-icon id="pencil"></automator-icon>
			</span>

			<span class="uap-conditions-pill-actions-remove">
				<automator-icon id="trash"></automator-icon>
			</span>
		`;

		return $wrapper;
	}
}
