// Import utilities
import { isDefined } from '@builder/common/utilities';
import recipeHasConditions from '@builder/item/button-conditions/utilities/recipe-has-conditions';

/**
 * Returns a conditions group given an ID
 *
 * @param {Object} options                   The options
 * @param {String} options.conditionsGroupId The ID of the conditions group
 * @return {ConditionsGroup}                 The conditions group
 */
const getConditionsGroupById = ( { conditionsGroupId } ) => {
	// Default response
	let conditionsGroup = {};

	// Check if the recipe has conditions
	if ( recipeHasConditions() ) {
		// Check if the conditions group exists
		if ( isDefined( UncannyAutomator.recipe.conditions[ conditionsGroupId ] ) ) {
			// Found it. Save it
			conditionsGroup = UncannyAutomator.recipe.conditions[ conditionsGroupId ];
		}
	}

	return conditionsGroup;
};

export default getConditionsGroupById;
