// Import utilities
import { isDefined } from '@builder/common/utilities';
import getUniqueId from '@builder/utilities/get-unique-id';
import getConditionPostData from '@builder/item/button-conditions/utilities/get-condition-post-data';
import parseDynamicSentence from '@builder/utilities/parse-dynamic-sentence';

/**
 * Creates a condition
 */
export default class Condition {
	// Private properties
	// Integration code. Use .integrationCode
	#_integrationCode = '';

	// Condition code. Use .conditionCode
	#_conditionCode = '';

	// Fields. Use .fields
	#_fields = {};

	/**
	 * Creates an unique ID
	 *
	 * @return {undefined}
	 */
	constructor() {
		// Create unique ID
		this.id = getUniqueId();
	}

	/**
	 * Sets the integration code
	 *
	 * @param {String} code The integration code
	 * @return {undefined}
	 */
	set integrationCode( code ) {
		// Check if the integration code is valid
		if ( isDefined( UncannyAutomator.integrations[ code ] ) ) {
			// Set integration code
			this.#_integrationCode = code;
		}
	}

	/**
	 * Returns the integration code
	 *
	 * @return {String} The integration code
	 */
	get integrationCode() {
		return this.#_integrationCode;
	}

	/**
	 * Sets the condition code
	 *
	 * @param {String} code The condition code
	 * @return {undefined}
	 */
	set conditionCode( code ) {
		// Set condition code
		this.#_conditionCode = code;
	}

	/**
	 * Returns the condition code
	 *
	 * @return {String} The condition code
	 */
	get conditionCode() {
		return this.#_conditionCode;
	}

	/**
	 * Sets the fields
	 *
	 * @param {Object} fieldsValues The fields data
	 * @return {undefined}
	 */
	set fields( fieldsValues ) {
		// Set condition code
		this.#_fields = fieldsValues;
	}

	/**
	 * Returns the fields values
	 *
	 * @return {Object} The fields values
	 */
	get fields() {
		return this.#_fields;
	}

	/**
	 * Returns an object with sanitazed data for the backend
	 *
	 * @return {Object} Sanitazed data
	 */
	get formattedForMeta() {
		// Default response
		let conditionForMeta = {};

		/**
		 * The backend expects
		 *
		 * {
		 * 	 integration: 'WP', // String with the integration code
		 * 	 condition: 'TOKEN_MEETS_CONDITION', // String with the condition code
		 * 	 fields: { // Object with the field values
		 * 	   FIELD_ID: 'Field value',
		 * 	   FIELD_ID_2: 'Field value'
		 * 	 }
		 * }
		 */
		conditionForMeta = {
			integration: this.integrationCode,
			condition: this.conditionCode,
			fields: this.fields,
		};

		return conditionForMeta;
	}

	/**
	 * Returns the dynamic sentence of the condition with the current values of the fields
	 *
	 * @return {Node} A node with the sentence
	 */
	get $dynamicSentence() {
		// Get condition post data
		const conditionPostData = getConditionPostData( {
			integrationCode: this.integrationCode,
			conditionCode: this.conditionCode,
		} );

		// Create node
		const $sentence = parseDynamicSentence( {
			dynamicSentence: conditionPostData.dynamic_name,
			fields: this.fields,
		} );

		return $sentence;
	}
}
