// Import utilities
import { isDefined, restCall } from '@builder/common/utilities';
import isAutomatorProActive from '@builder/utilities/is-automator-pro-active';
import sanitizeConditionsForMeta from '@builder/item/button-conditions/utilities/sanitize-conditions-for-meta';
import Actions from '@builder/item/actions';

/**
 * Updates the conditions saved in the recipe meta
 *
 * @param {Object}   options           The options
 * @param {Callback} options.onSuccess The callback invoked if the task is successful
 * @param {Callback} options.onFail    The callback invoked if the task is not successful
 * @return {undefined}
 */
const updateRemoteConditionsMeta = ( { onSuccess, onFail } ) => {
	// Update only if Pro is active
	if ( isAutomatorProActive() ) {
		// Get conditions
		const conditions = sanitizeConditionsForMeta();

		// Do REST call
		restCall(
			// Endpoint
			'actions_conditions_update',

			// Data
			{
				// eslint-disable-next-line camelcase
				recipe_id: UncannyAutomator.recipe.id,
				// eslint-disable-next-line camelcase
				actions_conditions: JSON.stringify( conditions ),
			},

			// On success
			( response ) => {
				// Check if the task was successful
				if ( response.success ) {
					// Check if the onSuccess callback is defined
					if ( isDefined( onSuccess ) ) {
						try {
							// Invoke onSuccess callback
							onSuccess( response );
						} catch ( e ) {
							if ( UncannyAutomator.dev.developerMode ) {
								// eslint-disable-next-line no-console
								console.error( "updateRemoteConditionsMeta: Couldn't execute the onSuccess callback." );

								// eslint-disable-next-line no-console
								console.error( e );
							}
						}
					}

					// Trigger custom event
					const event = new CustomEvent( 'automator/action/conditions/update', {
						detail: {
							conditions: conditions,
						},
					} );

					// Dispatch event
					document.dispatchEvent( event );

					// Render the Actions metabox again
					new Actions();
				}
			},

			// On fail
			() => {
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					console.error(
						"updateRemoteConditionsMeta: Couldn't update remote conditions meta. UI might be outdated."
					);
				}

				// Check if the onFail callback is defined
				if ( isDefined( onFail ) ) {
					try {
						// Invoke onFail callback
						onFail();
					} catch ( e ) {
						if ( UncannyAutomator.dev.developerMode ) {
							// eslint-disable-next-line no-console
							console.error( "updateRemoteConditionsMeta: Couldn't execute the onFail callback." );

							// eslint-disable-next-line no-console
							console.error( e );
						}
					}
				}
			}
		);
	}	
};

export default updateRemoteConditionsMeta;
