import $ from 'jquery';

// Import sprintf-js
const vsprintf = require( 'sprintf-js' ).vsprintf;

import {
	getDOMElements,
	restCall,
	isDefined,
	isEmpty,
	parseStringWithTokens,
	getCurrentFieldsValues
} from '../common/utilities.js';

import Modal    from '../common/modal.js';
import Markdown from '../common/markdown.js';

import {
	validateItemFields,
} from '../validation/validation.js';

import Triggers from './triggers.js';
import Actions  from './actions.js';

class ItemOptions {
	constructor( itemId, optionId ){
		this.itemId   = itemId;
		this.optionId = optionId;
		this.item 	  = UncannyAutomator.recipe.items[ itemId ];

		// Array to save the Field objects
		this.fields   = this.item.options[ this.optionId ].fields;
		// Array to save the buttons
		this.buttons  = this.item.options[ this.optionId ].buttons;

		this.create();

		// AFTER we added the elements to the DOM let's call init 
		// TokenSelector, TinyMCE and CodeMirror. We're going to call this in
		// ItemOptions.js because the TokenSelector, TinyMCE and 
		// CodeMirror need the item in the DOM before changing it
		// Select2 and the URL checker are called in Field.js
		this.initTokenSelector();
		this.initTinyMCE();
		this.initCodeMirror();

		// Dispatch custom event when everything is rendered
		document.dispatchEvent(
			new CustomEvent(
				'automator/item/options/open',
				{
					detail: {
						itemId: this.itemId,
						librariesReady: true
					}
				}
			)
		);
	}

	create(){
		// Container
		let container = getDOMElements( 'item', {
			id: this.itemId
		}).contentContainer;

		let itemOptions	= $( '<div/>', { 'class': 'item-options' });

		// Buttons. We have to create these before the fields, but append after
		let buttons = this.createButtons();

		// Fields
		let fields = this.createFields();
		this.fieldsContainer = fields;
		itemOptions.append( fields );

		// Check if we have to inject custom HTML
		if ( isDefined( this.item.customHTML ) ) {
			try {
				// Try to modify the content to insert item data
				try {
					this.item.customHTML = this.item.customHTML.replaceAll( '{{item_id}}', this.itemId );
				} catch ( e ) {
					console.log( e );
				}

				// Inject custom HTML
				itemOptions.get(0)
					.insertAdjacentHTML( 'beforeEnd', this.item.customHTML );
			} catch ( e ) {
				console.log( e );
			}
		}

		// Create notices container
		let noticeContainer  = $( '<div/>', { 'class': 'item-options__notices' });
		this.noticeContainer = noticeContainer;
		itemOptions.append( noticeContainer );

		// Append buttons
		itemOptions.append( buttons );

		// Append to the item
		container.html( itemOptions );
	}

	createFields(){
		let container = $( '<div/>', { 'class': 'item-options__group' });

		// Create fields
		$.each( this.fields, ( index, field ) => {
			// Get field type
			let fieldType = field.getAttributes().fieldType;

			// Check if it's a repeater
			if ( fieldType == 'repeater' ){
				// Create the fields again first (to avoid showing unsaved changed)
				field.createFields();
			}

			// Append the form element to the DOM
			field.createFormElement();
			container.append( field.getFormElement() );

			// Bind submit on Enter (for all except groups and textareas)
			if ( ! [ 'textarea', 'group', 'repeater' ].includes( fieldType ) ){
				// Get field element and bind 'keyup'
				field.getField().on( 'keyup', ( event ) => {
					// Check if it's an enter
					if ( event.keyCode == 13 ){
						// Trigger a click on the Save Button
						this.saveButton.trigger( 'click' );
					}
				});
			}
			// Check if it's a group
			else if ( fieldType == 'group' ){
				// Iterate fields
				field.fields.forEach(( field ) => {
					// Get the field type
					const fieldType = field.getAttributes().fieldType;

					// Check if it's not a textarea
					if ( fieldType !== 'textarea' ){
						// Get field element and bind 'keyup'
						field.getField().on( 'keyup', ( event ) => {
							// Check if it's an enter
							if ( event.keyCode == 13 ){
								// Trigger a click on the Save Button
								this.saveButton.trigger( 'click' );
							}
						});
					}
				});
			}
			else if ( fieldType == 'repeater' ){
				// Iterate rows
				field.fieldRows.forEach(( row, index ) => {
					// Iterate each row
					row.forEach(( Field, index ) => {
						// Get field element and bind 'keyup'
						Field.getField().on( 'keyup', ( event ) => {
							// Check if it's an enter
							if ( event.keyCode == 13 ){
								// Trigger a click on the Save Button
								this.saveButton.trigger( 'click' );
							}
						});
					});
				});
			}
		});

		return container;
	}

	createButtons(){
		// Buttons
		let container 	  	 = $( '<div/>', { 'class': 'item-options__buttons' });

		// Actions buttons
		let automatorButtons = $( '<div/>', { 'class': 'item-options__automator-buttons' }),
			developerButtons = $( '<div/>', { 'class': 'item-options__developer-buttons' });

		let saveButton 	  	 = $( '<div/>', { 'class': 'uap-btn uap-btn--primary' }),
			cancelButton  	 = $( '<div/>', { 'class': 'uap-btn uap-btn--secondary' }).text( UncannyAutomator.i18n.cancel );

		if ( this.item.itemType == 'trigger' ){
			saveButton.text( UncannyAutomator.i18n.save );
		}

		if ( this.item.itemType == 'action' ){
			saveButton.text( UncannyAutomator.i18n.save );
		}

		if ( this.item.itemType == 'closure' ){
			saveButton.text( UncannyAutomator.i18n.save );
		}

		this.saveButton = saveButton;

		automatorButtons.append( saveButton );
		automatorButtons.append( cancelButton );

		// Check developer defined buttons
		$.each( this.buttons, ( index, button ) => {
			// Create button
			const $buttonElement = $( '<div/>', { 'class': 'uap-btn' }).text( button.text );

			// Check if we have to add a custom class to the button
			if ( isDefined( button.css_classes ) ){
				// Add classes
				$buttonElement.addClass( button.css_classes );
			}

			// Check if it has a defined onClick function
			let doOnClick = () => {};
			if ( isDefined( button.on_click ) ){
				// Try to overwrite empty function
				try {
					// Try to convert the string to function, but first remove the <script> tags and trim the string
					doOnClick = eval( `(${ button.on_click.replace( '<script>', '' ).replace( '</script>', '' ).trim() })` ); 
				} catch ( e ){
					// Continue regardless of error
		
					// Output error if the developer mode is enabled
					if ( UncannyAutomator.dev.developerMode ){
						// eslint-disable-next-line no-console
						console.warn( e );
						console.warn( button.on_click );
					}
				}
			}

			// Listen clicks on the button
			$buttonElement.on( 'click', () => {
				// Invoke callback
				doOnClick(
					// button
					$buttonElement,
					// data
					{
						item:      this.item,
						values:    getCurrentFieldsValues( this.itemId ),
					},
					// modules
					(function(){
						// Get required modules
						let modules = {};

						// Check if the developer wants custom modules
						if ( isDefined( button.modules ) ){
							// Check if we have to add the modal module
							if ( button.modules.includes( 'modal' ) ){
								// Include modal
								modules.Modal = Modal;
							}

							if ( button.modules.includes( 'markdown' ) ){
								// Include Markdown
								modules.Markdown = Markdown;
							}
						}

						// Return modules
						return modules;
					})()
				);
			});

			// Add button to the container
			developerButtons.append( $buttonElement );
		});

		container.append( automatorButtons );
		container.append( developerButtons );

		saveButton.on( 'click', () => {
			// Verify form
			let formValidation = validateItemFields( this.fieldsContainer );

			// Check if it's valid
			if ( formValidation.isValid ){
				// Disable button and show loading animation
				saveButton.addClass( 'uap-btn--loading uap-btn--disabled' );

				// The REST call wants:
				// - The item ID
				// - The option code (the one from the token, the main one)
				// - The value. If there is more than one then a serialized object
				let itemId 		= this.itemId,
					optionCode 	= this.optionId,
					optionValue = this.getOptionValue();

				// Get a temporary item sentence
				// This must use values that are not yet saved in the meta of the item
				const itemSentence = parseStringWithTokens( this.item, this.item.meta.sentence, $.extend({}, this.item.meta, getCurrentFieldsValues( this.itemId ) ) );

				// Get the item sentence HTML
				let itemSentenceHTML;
				try {
					itemSentenceHTML = itemSentence.html.get(0).outerHTML;
				} catch ( e ){
					// Continue regardless of error
		
					// Output error if the developer mode is enabled
					if ( UncannyAutomator.dev.developerMode ){
						// eslint-disable-next-line no-console
						console.warn( e );
					}
				}

				restCall( 'update', {
					itemId:       itemId,
					optionCode:   optionCode,
					optionValue:  optionValue,
					sentence_human_readable: itemSentence.brackets,
					sentence_human_readable_html: itemSentenceHTML
				}, ( response ) => {
					// Trigger "saved" on all item fields
					this.fields.forEach(( Field ) => {
						Field.getFormElement().trigger( 'saved' );
					});

					if ( this.item.itemType == 'trigger' ){
						new Triggers();
					}

					if ( this.item.itemType == 'action' || this.item.itemType == 'closure' ){
						new Actions();
					}

					// Determine if the item is already valid
					// This will be true only if the new item doesn't have
					// options, or all the options have a default value
					const isValid = UncannyAutomator.recipe.items[ this.item.id ].isValid;

					// Get item form element
					const $item = getDOMElements( 'item', {
						id: this.item.id
					});

					// Check if it's valid
					if ( isValid ){
						// Check if the recipe is NOT live
						if ( ! UncannyAutomator.recipe.isLive ){
							// Check if the ITEM isn't already live
							// Otherwise, we don't have to trigger it again
							if ( ! this.item.isLive ){
								// Set the item to live
								$item.item
									.find( '.item-status input[type="checkbox"]' )
									.prop( 'checked', true )
									.trigger( 'change' );
								}
						}	
					}
					else {
						// Get first token and trigger a click
						// This will open the first group of options
						$item.item
							.find( '.item-title__token:not(.item-title__token--filled)' )
							.first()
							.trigger( 'click' );
					}
				}, ( response ) => {
					console.log( ' ERROR-TODO. Couldn\'t save the options of the item' );

					// Reset UI
					if ( this.item.itemType == 'trigger' ){
						new Triggers();
					}

					if ( this.item.itemType == 'action' || this.item.itemType == 'closure' ){
						new Actions();
					}

					// Try to open the options again
					try {
						// Get the item elements
						getDOMElements( 'item', {
							id: this.itemId
						})
						// Get the main container
						.item
						// Find the token in the sentence
						.find( `.item-title__token[data-options-id="${ optionCode }"]` )
						// Trigger a click
						.trigger( 'click' ); 
					} catch ( e ){
						// Continue regardless of error
		
						// Output error if the developer mode is enabled
						if ( UncannyAutomator.dev.developerMode ){
							// eslint-disable-next-line no-console
							console.warn( e );
						}
					}
				});
			}
		});

		cancelButton.on( 'click', () => {
			// Reset UI
			if ( this.item.itemType == 'trigger' ){
				new Triggers();
			}

			if ( this.item.itemType == 'action' || this.item.itemType == 'closure' ){
				new Actions();
			}
		});

		return container;
	}

	initTokenSelector(){
		$.each( this.fields, ( index, field ) => {
			// Get the field type
			field.initTokenSelector();		
		});
	}

	initTinyMCE(){
		$.each( this.fields, ( index, field ) => {
			// Get the field type
			if ( field.getAttributes().fieldType == 'textarea' ){
				// TinyMCE will be initialized only if it's a textarea and supportsTinyMCE is true
				field.initTinyMCE();
			}			
		});
	}

	initCodeMirror(){
		$.each( this.fields, ( index, field ) => {
			// Get the field type
			field.initCodeMirror();
		});
	}

	/**
	 * Get the value that we have to send to the REST call
	 *
	 * @since 0.5
	 */

	getOptionValue(){
		let response = {},
			hasSelect = false,
			hasRepeater = false;

		$.each( this.fields, ( index, field ) => {
			let id 	  = field.getAttributes().optionCode,
				value = field.getValue();

			if ( [ 'select', 'radio' ].includes( field.getAttributes().fieldType ) ){
				// Has select
				hasSelect = true;

				// Check if it's a multiselect
				// Then save the value as a JSON string, don't save it
				// as an array
				if ( field.getAttributes().supportsMultipleValues ){
					try {
						value = JSON.stringify( value );
					} catch ( e ){
						// Continue regardless of error
		
						// Output error if the developer mode is enabled
						if ( UncannyAutomator.dev.developerMode ){
							// eslint-disable-next-line no-console
							console.warn( e );
						}
					}
				}

				// Save readable value
				response[ vsprintf( '%s_readable', [ id ] ) ] = field.getReadableValue();

				// Check if we have to save the custom value (only for selects)
				const customValue = field.getSelectCustomValue();
				if ( value == 'automator_custom_value' ){
					response[ vsprintf( '%s_custom', [ id ] ) ] = customValue;
				}
			}
			else if ( field.getAttributes().fieldType == 'repeater' ){
				// Has repeater
				hasRepeater = true;
				
				// Convert data to a JSON string
				value = JSON.stringify( value );
			}

			response[ id ] = value;
		});

		if ( ! hasRepeater && ! hasSelect && Object.keys( response ).length == 1 ){
			// One field
			response = response[ Object.keys( response )[ 0 ] ];
		}

		return response;
	}
}

export default ItemOptions;