// Import utilities and components
import { openInNewTab, addUTMParameters } from '@builder/common/utilities';
import canManageConditions from '@builder/item/button-conditions/utilities/can-manage-conditions';
import ItemConditionsModalList from '@builder/item/button-conditions/modal-list';
import ConditionsGroup from '@builder/item/button-conditions/conditions-group';

/**
 * Adds the "Filter" button
 */
export default class ItemConditionsButton {
	/**
	 * Invokes the main methods that create the button and adds the callback
	 * to the button
	 *
	 * @param {Object}  options        The options
	 * @param {Integer} options.itemId The item ID
	 * @return {undefined}
	 */
	constructor( { itemId } ) {
		// Save the item data
		this.item = UncannyAutomator.recipe.items[ itemId ];

		// Create button
		this.createButton();

		// Handle click
		this.handleClick();

		// Disable when the user is editing the item
		this.disableWhenUserEditsAction();
	}

	/**
	 * Creates the button node
	 *
	 * @return {undefined}
	 */
	createButton() {
		// Create wrapper
		const $wrapper = document.createElement( 'div' );

		// Add classes
		$wrapper.classList.add( 'uap-item-button', 'uap-item-button--conditions' );

		// Add button inside
		$wrapper.innerHTML = `
			<automator-button
				color="secondary"
				size="extra-small"
				class="uap-conditions-action-button"
			>
				<automator-icon id="code-merge"></automator-icon>${ UncannyAutomator.i18n.conditions.filter }
			</automator-button>
		`;

		// Get the button
		const $button = $wrapper.querySelector( 'automator-button' );

		// Check if the user can perform this action
		if ( ! canManageConditions() ) {
			// Disable the button
			$button.setAttribute( 'disabled', '' );

			// Add tooltip
			$wrapper.setAttribute( 'uap-tooltip', UncannyAutomator.i18n.conditions.unlockConditions );
		}

		// Save it in an instance
		this.$wrapper = $wrapper;
		this.$button = $button;
	}

	/**
	 * Handle clicks to the button
	 *
	 * @return {undefined}
	 */
	handleClick() {
		// Add event listener
		this.$button.addEventListener( 'click', () => {
			// Check if the user can perform this action
			if ( canManageConditions() ) {
				// Create the conditions group
				this.#createConditionsGroup();

				// Go to the next step
				this.renderListOfConditions();
			} else {
				// Promote Pro
				this.promotePro();
			}
		} );
	}

	/**
	 * Goes to the next step and renders list of conditions
	 *
	 * @return {undefined}
	 */
	renderListOfConditions() {
		// Trigger custom event
		const event = new CustomEvent( 'automator/action/conditions/add', {
			detail: {
				conditionsGroupId: this.conditionsGroupId,
			},
		} );

		// Dispatch event
		document.dispatchEvent( event );

		// Create the modal with the list of conditions
		new ItemConditionsModalList( {
			conditionsGroupId: this.conditionsGroupId,
			comingFrom: 'action-filter-button',
		} );
	}

	/**
	 * Disables the button when the user is editing the action
	 * 
	 * @return {undefined}
	 */
	disableWhenUserEditsAction() {
		// Listen when an item is opened
		document.addEventListener( 'automator/item/options/open', ( event ) => {
			// Check if it's the action where this button is
			if ( this.item.id == event.detail.itemId ) {
				// Disable the button
				this.$button.setAttribute( 'disabled', '' );

				// Add a tooltip with the "Please save the action first" message
				this.$wrapper.setAttribute( 'uap-tooltip', UncannyAutomator.i18n.conditions.pleaseSaveActionFirst );
			}
		} );
	}

	/**
	 * Creates a ConditionsGroup for this action
	 *
	 * @return {undefined}
	 */
	#createConditionsGroup() {
		// Create the conditions group
		const conditionsGroup = new ConditionsGroup();

		// Add the actions
		conditionsGroup.actions = [ this.item.id ];

		// Save the ID
		this.conditionsGroupId = conditionsGroup.id;
	}

	/**
	 * Peforms the actions to promote the Pro version
	 *
	 * @return {undefined}
	 */
	promotePro() {
		// Open URL on a new tab
		openInNewTab(
			// Add UTM parameters
			addUTMParameters( 'https://automatorplugin.com/pricing/', {
				medium: 'recipe_creator',
				content: 'add_condition_btn',
			} )
		);
	}

	/**
	 * Determines if the button should be added to an item
	 *
	 * @return {Boolean} True if we should add the button
	 */
	shouldAddButton() {
		return (
			// Check if it's an action
			this.item.itemType == 'action' &&
			// Check that it DOESN'T have conditions already
			! this.item.hasConditions && 
			// The action is not missing
			! this.item.isMissing
		);
	}
}
