import $ from 'jquery';

import {
	getDOMElements,
	isEmpty,
	isDefined,
	restCall
} from '../common/utilities.js';

const sortable = require( 'html5sortable' );

import ItemHeaderMenu     from '../item/action-menu.js';
import ItemHeaderIcon     from '../item/integration-icon.js';
import ItemHeaderTag      from '../item/integration-name.js';
import ItemHeaderAsync    from '../item/button-schedule.js';
import ItemHeaderSentence from '../item/sentence.js';
import ItemReorder    	  from '../item/handle-reorder.js';
import Actions            from '../item/actions.js';

import getActionsOrder from '@builder/item/sortability/utilities/get-actions-order';

// Import "Filter" button
import ItemConditionsButton from '@builder/item/button-conditions/button';

class MetaboxItems {
	constructor( itemType, items, isSortable = false ){
	
		// Get the parent element for the items
		let metabox = getDOMElements( 'metabox', {
			type: itemType
		});

		// Count items
		const itemCount = Object.keys(items).length;

		// If there is more than one sortable item
		if ( itemCount > 1 && isSortable ) {

			// Create an inner container
			let innerContainer = $( '<div/>', {
				'class':   'sortable'
			});

			this.innerContainerSortable = innerContainer;

			// Sort items according to their menuOrder
			const sorted_items = Object.values(items).sort( ( a, b ) => {
				return a.menuOrder-b.menuOrder
			} );

			// Add each item to the container
			$.each( sorted_items, ( itemId, item ) => {
				if ( itemType == item.itemType ){
					// Create item
					let _item = this.createItem( item.id, isSortable );
	
					innerContainer.append( _item );
					
				}
			});

			// Append the container to the metabox
			metabox.append( innerContainer );
		
		} else {
			// Otherwise, simply add the items
			$.each( items, ( itemId, item ) => {
				if ( itemType == item.itemType ){
					// Create item
					let item = this.createItem( itemId );
	
					metabox.append( item );
					
				}
			});
		}
				
	}

	createItem( itemId, isSortable){
		// Get item data
		let item = UncannyAutomator.recipe.items[ itemId ];

		// Create container
		let container = $( '<div/>', {
			'class':   'item uap-item',
			'data-id': itemId
		});

		// Add data for devs to add custom CSS
		container[0].setAttribute( 'data-integration', item.integrationCode );
		container[0].setAttribute( 'data-item', item.itemCode );

		// Add property to the element
		container[0].itemId = itemId;

		if ( item.isMissing ){
			container.addClass( 'item--missing' );
		}

		if ( item.meta.async_mode ){
			container.addClass( 'item--hasSchedule' );
		}

		// Replace tokens in the CSS
		if ( ! isEmpty( item.inlineCss ) ){
			item.inlineCss = item.inlineCss.replace( /{{item_id}}/g, itemId );

			// Add inline inlineCss
			container.prepend( item.inlineCss );
		}

		// Create header
		let header = this.createInner( itemId );

		let notice = this.createNotice( item );

		if ( isSortable ) {
			// Check if it's an action, and DOESN'T have conditions
			// For now, we won't allow users to drag actions inside conditions
			if ( item.itemType == 'action' && ! item.hasConditions ) {
				let dragHandleButton = new ItemReorder( itemId );
				container.append( dragHandleButton );
			}
		}

		container.append( header );
		container.append( notice );

		container.on( 'automator:item:open', (e, open) => {
			let asyncButtons = container.find('.item-async');
			$( asyncButtons[0] ).trigger( 'automator:asyncButton:maybeDisable', open );           
		})

		return container;
	}

	/**
	 * Creates item header
	 * 
	 * @since 0.5
	 *
	 * @param {string}    integrationCode - The integration code
	 * @param {string}    itemType - The item type: 'trigger', 'action' or 'closure'
	 * @param {string}    itemCode - The code of the item
	 * @param {int}       itemId - The ID of the item
	 */
	createInner( itemId ){
		// Get item data
		const item = UncannyAutomator.recipe.items[ itemId ];

		// If the item is missing, override the integrationCode
		const integrationCode = item.isMissing ? null : item.integrationCode;

		// Get content
		const content = {
			// The sentence of the trigger/action/closure
			$sentence: new ItemHeaderSentence( item.itemType, integrationCode, item.itemCode, itemId ),

			// Icon. The integration icon
			$icon: new ItemHeaderIcon( integrationCode ),

			// Integration name tag
			$integrationNameTag: new ItemHeaderTag( {
				integrationId: integrationCode,
				customIntegrationName: item.integrationName
			} ),

			// The action menu. The three dots on the upper right corner.
			$actionMenu: new ItemHeaderMenu( item.itemType, integrationCode, item.itemCode, itemId ),
			
			// Buttons. These are added next to the action menu
			buttons: []
		}

		// Create "Filter" button
		const filterButton = new ItemConditionsButton( {
			itemId: itemId
		} );

		// Check if we have to add the button
		if ( filterButton.shouldAddButton() ) {
			// Add the button
			content.buttons.push( filterButton.$wrapper );
		}

		// Create "Schedule" button
		const scheduleButton = new ItemHeaderAsync( item.itemType, integrationCode, item.itemCode, itemId );

		// Check if we have to add the button
		if ( scheduleButton.shouldAddButton() ) {
			// Add the button
			content.buttons.push( scheduleButton.$button );
		}

		// Create content
		const $template = document.createElement( 'div' );
		$template.classList.add( 'uap-item-inner' );

		// Add content
		$template.insertAdjacentHTML( 'beforeEnd', `
			<div class="uap-item-sidebar"></div>
			<div class="uap-item-top-header">
				<div class="uap-item-top-header-left"></div>
				<div class="uap-item-top-header-right"></div>
			</div>
			<div class="uap-item-main"></div>
			<div class="uap-item-inner-content"></div>
		` );

		// Icon. The integration icon
		$template.querySelector( '.uap-item-sidebar' )
			.appendChild( content.$icon );

		// Add integration name tag
		$template.querySelector( '.uap-item-top-header-left' )
			.appendChild( content.$integrationNameTag );

		// Add action menu
		$template.querySelector( '.uap-item-top-header-right' )
			.appendChild( content.$actionMenu.get(0) );

		// The sentence of the trigger/action/closure
		$template.querySelector( '.uap-item-main' )
			.appendChild( content.$sentence.get(0) );

		// Add buttons
		content.buttons.forEach( ( $button ) => {
			// Append the button
			$template.querySelector( '.uap-item-top-header-right' )
				.appendChild( $button );
		} );

		return $template;
	}

	/**
	 * Create item notice
	 *
	 * @since 0.6
	 *
	 */

	createNotice( item ){
		let container;

		if ( item.isMissing ){
			container = $( '<automator-alert/>', {
				type: 'warning'
			});
			
			container.text( UncannyAutomator.i18n.itemMissing );
		}

		return container;
	}

	/**
	 * Create item notice
	 *
	 * @since 3.5
	 *
	 */

	 makeSortable() {
		// Make actions sortable
		// Use sortable from the html5sortable library
		sortable( this.innerContainerSortable, {
			placeholder: '<div class="item"></div>',
			handle: '.item-handle, .uap-conditions-block-drag-handle',
			items: '#recipe-actions-ui .ua-sortable > .item, .uap-conditions-block',
			hoverClass: 'hover', 
		} )[0].addEventListener('sortupdate', (e) => {
			this.orderUpdated( e );
		});
	}

	orderUpdated( e ) {
		// Get the order of the actions
		const actionsOrder = getActionsOrder();

		// Send the array with IDs in the new order to the plugin
		restCall(
			'actions_order',
			{
				'recipeID': UncannyAutomator.recipe.id,
				'newOrder': actionsOrder
			},
			( response ) => {
				// In case of success
				new Actions();
			},
			( response ) => {
				// In case of a failure
				new Actions();
			}
			
		);
	}
}

export default MetaboxItems;