/**
 * Parses string with SENTENCE tokens
 *
 * @param {Object} item   The item
 * @param {String} string The string with the tokens
 * @param {Object} data   Object with the data to replace the text in the tokens.
 * @return {Object}  response                   Object with information about the string
 *         {String}  response.html              The string with the tokens replaced with the data and enclosed in HTML tags
 *         {Object}  response.elements          Node parts of the sentence
 *         {Array}   response.elements.tokens   Array of jQuery object. All the tokens part of the sentence
 *         {Array}   response.parts             Object with information about each part of the sentence
 */
export const parseStringWithTokens = ( item = null, string = '', data = {} ) => {
	let response = null;

	if (
		/* isDefined( UncannyAutomator.recipe.items[ item.id ].itemSentence )
        && forceReDo == false */
		false
	) {
		response = UncannyAutomator.recipe.items[ item.id ].itemSentence;
	} else {
		// Create response object
		response = {
			plain: '',
			brackets: '',
			html: $( '<div/>' ),
			elements: {
				normal: [],
				token: [],
			},
			parts: [],
		};

		// Trim string
		string = string.trim();

		// Check if the string isn't empty
		if ( ! isEmpty( string ) ) {
			// Create regex
			const tokensRegex = new RegExp( /\{\{(.*?)\}\}/gm ),
				normals = [],
				tokens = [];

			let mappedString = string;

			// Get tokens
			let tokenNumber = 0;

			string = string.replace( tokensRegex, ( fullMatch, token ) => {
				const tokenParts = token.split( ':' );

				// Get the token placeholder text
				const tokenPlaceholderText = tokenParts[ 0 ];

				// Get the token value ID
				const tokenValueID = tokenParts[ 1 ];

				// Get the ID of the group of fields this token has to open
				// Check if the token should open a different group of options
				const optionsID = isDefined( tokenParts[ 2 ] ) ? tokenParts[ 2 ] : tokenValueID;

				token = {
					type: 'token',
					id: tokenValueID,
					text: tokenPlaceholderText,
					optionsID: optionsID,
					hasFields: isDefined( item.options[ optionsID ] ),
				};

				// Check if we have data for the token
				if ( isDefined( data[ token.id ] ) ) {
					let readableText;

					if (
						data[ token.id ] == 'automator_custom_value' &&
						isDefined( data[ vsprintf( '%s_custom', [ token.id ] ) ] )
					) {
						readableText = data[ vsprintf( '%s_custom', [ token.id ] ) ];
					} else if ( isDefined( data[ vsprintf( '%s_readable', [ token.id ] ) ] ) ) {
						readableText = data[ vsprintf( '%s_readable', [ token.id ] ) ];
					}

					token.originalText = token.text;
					token.text = isDefined( readableText ) ? readableText : data[ token.id ];
				}

				// Add token
				tokens.push( token );

				// Add token to the mapped string
				mappedString = mappedString.replace( fullMatch, `{{token:${ tokenNumber }}}` );
				tokenNumber++;

				return '{{v}}';
			} );

			const stringParts = string.split( '{{v}}' );

			// Get normals
			let normalNumber = 0;
			$.each( stringParts, ( key, sentenceSection ) => {
				if ( sentenceSection != '' ) {
					// Create normal part
					const normal = {
						type: 'normal',
						text: sentenceSection,
					};

					// Add normal text
					normals.push( normal );

					// Add "normal" part to the mapped string
					mappedString = mappedString.replace( sentenceSection, `{{normal:${ normalNumber }}}` );
					normalNumber++;
				}
			} );

			// Create response.parts
			let match;

			while ( ( match = tokensRegex.exec( mappedString ) ) !== null ) {
				// Get info
				let part = match[ 1 ].split( ':' );

				part = {
					type: part[ 0 ],
					index: part[ 1 ],
				};

				// Get part object and add to parts
				if ( part.type == 'normal' ) {
					response.parts.push( normals[ part.index ] );
				} else {
					response.parts.push( tokens[ part.index ] );
				}
			}

			// Check if the sentence has tokens
			let itemTokens = null;

			// Create plain text and HTML versions
			$.each( response.parts, ( index, part ) => {
				// Plain text
				response.plain += part.text;

				// HTML
				if ( part.text != ' ' ) {
					let elementAttributes,
						text = part.text;

					let contentIsNode = false,
						tagNodeContent = null;

					if ( part.type == 'normal' || ( part.type == 'token' && ! part.hasFields && ! item.isMissing ) ) {
						elementAttributes = {
							class: 'item-title__normal',
						};

						// Add normal-text part to the brackets version
						response.brackets += part.text;
					} else {
						const CSSClass = 'item-title__token';

						elementAttributes = {
							class: CSSClass,
							'data-token-id': part.id,
							'data-options-id': part.optionsID,
						};

						// Check if it's using a valid ID
						if ( isDefined( item.options[ part.optionsID ] ) ) {
							// Check if the section is valid
							if ( item.options[ part.optionsID ].isValid ) {
								// Add filled class
								elementAttributes.class += ' item-title__token--filled';
							}
						}

						// Check if it's filled
						if ( isDefined( part.originalText ) ) {
							// Add filled class
							// elementAttributes[ 'class' ] += ' item-title__token--filled';

							// Check if the text is a token
							// let isValidToken = false;

							if ( tokensRegex.test( text ) ) {
								// Check if we already have the tokens data
								if ( ! isDefined( itemTokens ) ) {
									itemTokens = new Tokens( item.itemType, 'text', {
										includeGroupName: false,
									} );
								}

								// Check if it's a valid token
								const token = itemTokens.renderTextWithTokens( text );

								if ( isDefined( token ) ) {
									// Change the validity of the token
									// isValidToken = true;
									contentIsNode = true;
									tagNodeContent = token;
								}
							}

							if ( ! contentIsNode ) {
								// Define text of the token
								const abbreviatedText = abbreviateText( text, 30 );

								// Check if the text was long enough to abbreviate it
								if ( abbreviatedText.isAbbreviated ) {
									// Add tooltip with the complete text
									elementAttributes[ 'uap-tooltip' ] = text;

									// Replace original text
									text = abbreviatedText.text;
								}
							}
						}

						// Add token to the brackets version
						response.brackets += `{{${ decodeEntities( text ) }}}`;
					}

					const element = $( '<span/>', elementAttributes );

					if ( contentIsNode ) {
						element.append( tagNodeContent );
					} else {
						element.text( decodeEntities( text ) );
					}

					// Add to the elements array
					response.elements[ part.type ].push( element );

					// Append HTML
					response.html.append( element );
				} else {
					// Add white spaces to the brackets version
					response.brackets += part.text;
				}
			} );

			// Convert array of jQuery elements into wrapped set of elements
			response.elements.normal = $( response.elements.normal ).map( ( index, element ) => {
				return element.get();
			} );
			response.elements.token = $( response.elements.token )
				.map( ( index, element ) => {
					return element.get();
				} )
				.filter( ':not(.item-title__normal)' );
		}

		UncannyAutomator.recipe.items[ item.id ].itemSentence = response;
	}

	return response;
};

/**
 * Opens URL on a new centered window
 *
 * @param {String}   options         The options
 * @param {String}   options.url     The URL
 * @param {String}   options.title   The window title
 * @param {Integer}  options.w       The window width
 * @param {Integer}  options.h       The window height
 * @param {Function} options.onClose Callback invoked when the window is closed
 * @return {WindowProxy}               The window instance
 */
export const openCenteredWindow = ( { url, title, w, h, onClose } ) => {
	// Fixes dual-screen position
	const dualScreenLeft = isDefined( window.screenLeft ) ? window.screenLeft : window.screenX;
	const dualScreenTop = isDefined( window.screenTop ) ? window.screenTop : window.screenY;

	let width = document.documentElement.clientWidth ? document.documentElement.clientWidth : screen.width;
	if ( window.innerWidth ) {
		width = window.innerWidth;
	}

	let height = document.documentElement.clientHeight ? document.documentElement.clientHeight : screen.height;
	if ( window.innerHeight ) {
		height = window.innerHeight;
	}

	const systemZoom = width / window.screen.availWidth;
	const left = ( width - w ) / 2 / systemZoom + dualScreenLeft;
	const top = ( height - h ) / 2 / systemZoom + dualScreenTop;
	const newWindow = window.open(
		url,
		title,
		`
		scrollbars=yes,
		width=${ w / systemZoom }, 
		height=${ h / systemZoom }, 
		top=${ top }, 
		left=${ left }
		`
	);

	if ( window.focus ) {
		newWindow.focus();
	}

	// Check if there is an onClose event defined
	if ( isDefined( onClose ) ) {
		/**
		 * Checks if the window was closed
		 */
		const wasWindowClosed = () => {
			if ( newWindow.closed ) {
				onClose();
			} else {
				// Check in .5 seconds again
				setTimeout( () => {
					wasWindowClosed();
				}, 500 );
			}
		};

		wasWindowClosed();
	}

	return newWindow;
};

/**
 * Determines if a variable is set and is not NULL
 *
 * @param {Mixed} variable The variable being evaluated
 * @return {Boolean}           True if the variable is defined
 */
export const isDefined = ( variable ) => {
	// Returns true if the variable is undefined
	return typeof variable !== 'undefined' && variable !== null;
};

/**
 * Determines whether a variable is empty
 *
 * @param {Mixed} variable The variable being evaluated
 * @return {Boolean}           True if the variable is empty
 */
export const isEmpty = ( variable ) => {
	let response = true;

	// Check if the variable is defined, otherwise is empty
	if ( isDefined( variable ) ) {
		// Check if it's array
		if ( $.isArray( variable ) ) {
			response = variable.length == 0;
		} else if ( isObject( variable ) ) {
			response = $.isEmptyObject( variable );
		} else {
			response = variable == '';
		}
	}

	return response;
};

/**
 * Checks if two strings are equal
 *
 * @param {String}  string1                String to be compared
 * @param {String}  string2                String to be compared
 * @param {Object}  settings               Object with settings
 * @param {Boolean} settings.caseSensitive If true, uppercase and lowercase chars will be treated as distinct
 * @return {Boolean}                         True if both strings are equal
 */
export const compareStrings = ( string1, string2, settings = { caseSensitive: true } ) => {
	// Create response variable
	let response = false;

	// Check if it's case sensitive
	if ( ! settings.caseSensitive ) {
		response = string1.toString().toLowerCase() == string2.toString().toLowerCase();
	} else {
		response = string1.toString() == string2.toString();
	}

	return response;
};

/**
 * Checks if a value exists in an array
 * If the needle is an array then it will check if all his elements exists in the haystack
 *
 * @param {Mixed} needle   The searched value. The comparison between values is strict. If the needle is an array then it will check if all his elements exists in the haystack
 * @param {Array} haystack An array through which to search.
 * @return {Boolean}           True if needle is found in the array or if needle is an array, TRUE if all the elements from the needle are in haystack
 */
export const inArray = ( needle, haystack ) => {
	let response = false;

	if ( Array.isArray( needle ) ) {
		// Check that all the elements from the array "value" are in the array "array"
		response = arrayDifference( needle, haystack ).length == 0;
	} else {
		// Check if one element is in an array
		response = $.inArray( needle, haystack ) !== -1;
	}

	return response;
};

/**
 * Compares the values of two arrays and returns the difference
 * Compares array1 against one array and returns the values in array1 that are not present in array2.
 * Examples:
 * +-------------------+-------------+--------------+
 * |      array1       |   array2    |    return    |
 * +-------------------+-------------+--------------+
 * | [ 1, 2, 3, 4, 5 ] | [ 1, 3, 4 ] | [ 2, 5]      |
 * | [ 1, 2, 3 ]       | [ 2, 3 ]    | [ 1 ]        |
 * | [ 2, 3 ]          | [ 1, 2, 3 ] | []           |
 * | [ 1, 2 ]          | [ 1, 2 ]    | []           |
 * | [ '1', '2' ]      | [ 1, 2 ]    | [ '1', '2' ] |
 * +-------------------+-------------+--------------+
 *
 * @param {Array} array1 The array to compare from
 * @param {Array} array2 An array to compare against
 * @return {Array}          Array containing all the entries from array1 that are not present in array2
 */
export const arrayDifference = ( array1, array2 ) => {
	return $( array1 ).not( array2 ).get();
};

/**
 * Determines whether a variable is a string
 *
 * @param {Mixed} string The variable being evaluated
 * @return {Boolean}         True if the variable is an string
 */
export const isString = ( string ) => {
	return typeof string === 'string';
};

/**
 * Determines whether a variable is an object
 *
 * @param {Mixed} variable The variable being evaluated
 * @return {Boolean}           True if the variable is an object
 */
export const isObject = ( variable ) => {
	return variable !== null && typeof variable === 'object';
};

/**
 * Removes all spaces from a string
 *
 * @param {String} string The string
 * @return  {String}        String without spaces
 */
export const removeSpaces = ( string ) => {
	return string.replace( /\s/g, '' );
};

/**
 * Replaces curly braces
 *
 * @param {String} string The string to be searched
 * @param {String} start  The value to replace the for {{
 * @param {String} end    The value to replace the for }}
 * @return {String}          The value with the replaced values
 */
export const replaceCurlyBraces = ( string = '', start = '', end = '' ) => {
	string = string.replace( /{{/g, start );
	string = string.replace( /}}/g, end );
	return string;
};

/**
 * Creates a console log label
 *
 * @param {String}  text    Text to show after the label
 * @param {Boolean} isGroup Define if we have to do a console.group instead of console.log
 * @param {Object}  options The options
 */
export const consoleLogLabel = ( text = '', isGroup = false, options = {} ) => {
	// Output if the developer mode is enabled
	if ( UncannyAutomator.dev.developerMode ) {
		// Check if the user defined a custom background color
		if ( ! isDefined( options.bgColor ) ) {
			options.bgColor = '#0790e8';
		}

		// Define console log Automator style
		const labelStyle = `background: ${ options.bgColor }; color: #fff; font-weight: 500; padding: 2px 6px; font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif; border-radius: 8px;`;

		// Do console log
		if ( isGroup ) {
			if ( isDefined( options.collapseGroup ) && options.collapseGroup ) {
				// eslint-disable-next-line no-console
				console.groupCollapsed( '%cAutomator', labelStyle, text );
			} else {
				// eslint-disable-next-line no-console
				console.group( '%cAutomator', labelStyle, text );
			}
		} else {
			// eslint-disable-next-line no-console
			console.log( '%cAutomator', labelStyle, text );
		}
	}
};

/**
 * Creates a custom console log with colors to
 * represent success and failure
 *
 * @param {String} status Message type. Use 'success' or 'error'
 * @param {String} text   Text to show after the label
 */
export const consoleLogResult = ( status = 'error', text = '' ) => {
	// Output if the developer mode is enabled
	if ( UncannyAutomator.dev.developerMode ) {
		// Define console log Automator style
		let labelStyle = [
			'color: #fff',
			'padding: 2px 7px',
			// eslint-disable-next-line max-len
			'font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif',
			'font-weight: normal',
		].join( ';' );

		// Add error or success colors
		if ( status == 'error' ) {
			labelStyle = `background: #f13c3c; ${ labelStyle }`;
		}

		if ( status == 'success' ) {
			labelStyle = `background: #40b528; ${ labelStyle }`;
		}

		// Do console log
		// eslint-disable-next-line no-console
		console.log( `%c${ text }`, labelStyle );
	}
};

/**
 * Escape HTML characters
 *
 * @param {String} html The HTML
 * @return {String}      Scaped HTML
 */
export const escapeHTML = ( html = '' ) => {
	const tagsToReplace = {
		'&': '&amp;',
		'<': '&lt;',
		'>': '&gt;',
	};

	/**
	 * Replace tags
	 *
	 * @param {String} tag The tag to replace
	 * @return {String}     The tag replaced
	 */
	const replaceTag = ( tag ) => {
		return tagsToReplace[ tag ] || tag;
	};

	return html.replace( /[&<>]/g, replaceTag );
};

/**
 * Abbreviate long texts. This function will convert a string
 * like "123456789" to "123...789"
 *
 * @param {String}  text   The string
 * @param {Integer} length The max length of the string
 * @return {Object}        Object with two properties: isAbbreviated and text
 */
export const abbreviateText = ( text = '', length = 10 ) => {
	// Default response
	const response = {
		isAbbreviated: false,
		text: text,
	};

	// Only abbreviate the text if the text is longer than the length
	if ( text.length > length ) {
		// Get length of each parts (start and end)
		const partLengths = Math.floor( length / 2 );

		// Get parts
		const textParts = {
			start: text.substring( 0, partLengths ),
			end: text.slice( -partLengths ),
		};

		// Create abbrevited text
		response.text = `${ textParts.start } ... ${ textParts.end }`;
		response.isAbbreviated = true;
	}

	// Return response
	return response;
};

/**
 * Returns an unique ID
 *
 * @return {String} The ID
 */
export const getUniqueId = () => {
	// Math.random should be unique because of its seeding algorithm.
	// Convert it to base 36 (numbers + letters), and grab the first 9 characters
	// after the decimal.
	return `_${ Math.random().toString( 36 ).substr( 2, 9 ) }`;
};

/**
 * Inserts parameter(s) to an URL.
 * If the parameter already exists it will update it
 *
 * @param {String}  url              The URL
 * @param {Object}  parameters       The parameters
 * @param {Object}  options          The options.
 * @param {Boolean} options.setAsURL True to set the URL as the current URL
 * @param {Boolean} options.refresh  True to refresh the page after setting the URL
 * @return {String}                  The URL with the parameters
 */
export const addParametersToURL = ( url = '', parameters = {}, options = {} ) => {
	// If the URL is empty, use the site URL
	if ( isEmpty( url ) ) {
		url = window.location.href;
	}

	// Try to get an URL object
	try {
		url = new URL( url );
	} catch ( e ) {
		// If something goes wrong, just return the original URL
		return url;
	}

	// Get the current parameters in the URL
	let urlParameters = url.search.substr( 1 ).split( '&' );

	// Iterate the parameters
	for ( let [ key, value ] of Object.entries( parameters ) ) {
		key = encodeURI( key );
		value = encodeURI( value );

		let i = urlParameters.length;
		let x;

		while ( i-- ) {
			x = urlParameters[ i ].split( '=' );

			if ( x[ 0 ] == key ) {
				x[ 1 ] = value;
				urlParameters[ i ] = x.join( '=' );
				break;
			}
		}

		if ( i < 0 ) {
			urlParameters[ urlParameters.length ] = [ key, value ].join( '=' );
		}
	}

	// Remove empty parameters
	urlParameters = urlParameters.filter( ( parameter ) => {
		// Remove parameters without values
		const parameterParts = parameter.split( '=' );

		return ! ( isEmpty( parameter ) || isEmpty( parameterParts[ 1 ] ) );
	} );

	// Add parameters to the URL
	url.search = urlParameters.join( '&' );

	// Set default options
	options = $.extend(
		{},
		{
			setAsURL: false,
			refresh: false,
		},
		options
	);

	// Check if we have to set it as the current URL
	if ( options.setAsURL ) {
		// Check if we have to reload
		if ( options.refresh ) {
			// This will reload the page, it's likely better to store this until finished
			window.location.href = url.href;
		} else {
			// Get title of the current page
			const pageTitle = document.title;

			// Push history and update URL
			window.history.pushState( {}, pageTitle, url.href );
		}
	}

	// Return URL
	return url.href;
};

/**
 * Returns the value of a given parameter on a given URL
 *
 * @param {String} url       The URL containing the parameter
 * @param {String} parameter The parameter key
 * @return {String}           The parameter value
 */
export const getURLParameter = ( url = '', parameter = '' ) => {
	// If the URL is empty, use the site URL
	if ( isEmpty( url ) ) {
		url = window.location.href;
	}

	// Convert it to an URL object
	url = new URL( url );

	// Get parameter
	return url.searchParams.get( parameter );
};

/**
 * Decodes HTML entities
 *
 * @since  2.1.4
 * @param {String} encodedString The string with encoded entities
 * @return {String}               The same string, with the entities decoded
 */
export const decodeEntities = ( encodedString = '' ) => {
	const $textarea = document.createElement( 'textarea' );
	$textarea.innerHTML = encodedString;
	return $textarea.value;
};

/**
 * Throttles a function
 *
 * @param {Function} func     The function to throttle
 * @param {Integer}  interval The interval, in milliseconds
 * @return {Function}           The function throttled
 */
export const throttle = ( func, interval ) => {
	let lastCall = 0;

	return () => {
		const now = Date.now();
		if ( lastCall + interval < now ) {
			lastCall = now;
			// eslint-disable-next-line no-undef
			return func.apply( this, arguments );
		}
	};
};

/**
 * Debounces a function
 *
 * @param {Function} func     The function to debounce
 * @param {Integer}  interval The interval, in milliseconds
 * @return {Function}         The function debounced
 */
export const debounce = ( func, interval ) => {
	let lastCall = -1;

	return () => {
		clearTimeout( lastCall );
		// eslint-disable-next-line no-undef
		const args = arguments;
		const self = this;
		lastCall = setTimeout( () => {
			func.apply( self, args );
		}, interval );
	};
};

/**
 * Copies a string to the clipboard
 *
 * @param {String} string The string to copy
 * @return {undefined}
 */
export const copyToClipboard = ( string ) => {
	// Create temporal textarea
	const $tempTextarea = document.createElement( 'textarea' );

	// Make the textarea really small
	$tempTextarea.style.width = '0px';
	$tempTextarea.style.height = '0px';

	// Set the string as the textarea value
	$tempTextarea.value = string;

	// Add the textarea to the body
	document.body.appendChild( $tempTextarea );

	// Select all the text from the textarea
	$tempTextarea.select();

	// Copy content
	document.execCommand( 'copy' );

	// Remove the element from the body
	document.body.removeChild( $tempTextarea );
};

/**
 * Creates and show a modal with useful data when the JS throws a fatal error
 *
 * @param {Object} settings The settings
 * @return {undefined}
 */
export const handleFatalError = ( settings = {} ) => {
	settings = $.extend(
		true,
		{
			title: UncannyAutomator.i18n.debugging.fatalErrorHandler.title,
			warning: '',
			content: UncannyAutomator.i18n.debugging.fatalErrorHandler.content,
			console: null,
			buttons: {
				confirm: {
					label: UncannyAutomator.i18n.debugging.learnMore,
					onClick: () => {
						// Learn more
						window.open( UncannyAutomator.dev.debuggingURL, '_blank' );
					},
				},
				cancel: {
					label: UncannyAutomator.i18n.close,
					onClick: ( $modal ) => {
						// Destroy modal
						$modal.destroy();
					},
				},
			},
		},
		settings
	);

	// Create report log
	let reportLog = '';

	let recipeData;
	try {
		// Get the recipe data
		$.each( UncannyAutomator.recipes_object, ( index, theRecipe ) => {
			if ( theRecipe.ID == UncannyAutomator.recipe.id ) {
				recipeData = theRecipe;
			}
		} );
	} catch ( e ) {
		// Continue regardless of error

		// Output error if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// eslint-disable-next-line no-console
			console.warn( e );
		}
	}

	// Get general information about the recipe
	try {
		let logRecipe = `====== ${ UncannyAutomator.i18n.debugging.recipeTitle } ====== \n`;

		// Add recipe type
		let recipeType = UncannyAutomator.i18n.debugging.recipeTypeUser;
		if ( recipeData.recipe_type == 'anonymous' ) {
			recipeType = UncannyAutomator.i18n.debugging.recipeTypeAnonymous;
		}

		logRecipe += `${ UncannyAutomator.i18n.debugging.recipeType }: ${ recipeType }`;

		// Add recipe status
		let recipeStatus = UncannyAutomator.i18n.debugging.recipeStatusDraft;
		if ( recipeData.post_status == 'publish' ) {
			recipeStatus = UncannyAutomator.i18n.debugging.recipeStatusLive;
		}

		logRecipe += `\n${ UncannyAutomator.i18n.debugging.recipeStatus }: ${ recipeStatus }`;

		reportLog = logRecipe;
	} catch ( e ) {
		// Continue regardless of error

		// Output error if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// eslint-disable-next-line no-console
			console.warn( e );
		}
	}

	// Get information about the recipe items
	try {
		let logRecipeItems = `=== ${ UncannyAutomator.i18n.debugging.recipeItemsTitle } === \n`;

		/**
		 * Cleans extra data
		 *
		 * @param {Object} item The item
		 * @return {String}      Information about the item
		 */
		const cleanItemData = ( item ) => {
			// Get status
			const recipeStatus =
				item.post_status == 'publish'
					? UncannyAutomator.i18n.debugging.itemStatusLive
					: UncannyAutomator.i18n.debugging.itemStatusDraft;

			return `> ${ item.meta.integration } - ${
				item.meta.code
			} - ${ recipeStatus }\n  (${ item.meta.sentence.replace(
				/\{\{|\:[a-zA-Z0-9]*\}\}/g,
				''
			) })\n  ${ JSON.stringify( item )
				.replaceAll( '<', '&lt;' )
				.replaceAll( '>', '&gt;' ) }\n`;
		};

		// Get triggers
		const triggers = recipeData.triggers.map( cleanItemData );
		const hasTriggers = triggers.length > 0;

		// Get actions
		const actions = recipeData.actions.map( cleanItemData );
		const hasActions = actions.length > 0;

		// Get closures
		const closures = recipeData.closures.map( cleanItemData );
		const hasClosures = closures.length > 0;

		// Check if it has recipe items
		if ( hasTriggers || hasActions || hasClosures ) {
			// Add triggers
			logRecipeItems += `${ UncannyAutomator.i18n.debugging.triggersTitle }\n\n`;

			// Check if it has triggers
			if ( hasTriggers ) {
				logRecipeItems += triggers.join( '\n' );
			} else {
				logRecipeItems += `> ${ UncannyAutomator.i18n.debugging.noTriggers }`;
			}

			// Add actions
			logRecipeItems += `\n${ UncannyAutomator.i18n.debugging.actionsTitle }\n\n`;

			// Check if it has actions
			if ( hasActions ) {
				logRecipeItems += actions.join( '\n' );
			} else {
				logRecipeItems += `> ${ UncannyAutomator.i18n.debugging.noActions }`;
			}

			// Add closures
			logRecipeItems += `\n${ UncannyAutomator.i18n.debugging.closuresTitle }\n\n`;

			// Check if it has closures
			if ( hasClosures ) {
				logRecipeItems += closures.join( '\n' );
			} else {
				logRecipeItems += `> ${ UncannyAutomator.i18n.debugging.noClosures }`;
			}
		} else {
			logRecipeItems += `${ UncannyAutomator.i18n.debugging.noRecipeItemsFound }`;
		}

		reportLog = reportLog !== '' ? `${ reportLog }\n\n${ logRecipeItems }` : reportLog;
	} catch ( e ) {
		// Continue regardless of error

		// Output error if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// eslint-disable-next-line no-console
			console.warn( e );
		}
	}

	// Add JS console
	try {
		// Check if we have content to add
		if ( isDefined( settings.console ) ) {
			// Output only if the developer mode is enabled
			if ( UncannyAutomator.dev.developerMode ) {
				// eslint-disable-next-line no-console
				console.error( settings.console );
			}

			let jsConsole = `====== ${ UncannyAutomator.i18n.debugging.jsConsole } ====== \n`;

			// Add JS console
			try {
				jsConsole += `${ settings.console.stack.replaceAll( UncannyAutomator.siteURL, '{site_url}' ) }`;
			} catch ( e ){
				try {
					jsConsole += `${ settings.console.replaceAll( UncannyAutomator.siteURL, '{site_url}' ) }`;
				} catch ( e ){
					jsConsole += settings.console;
				}
			}
			
			reportLog = reportLog !== '' ? `${ reportLog }\n\n${ jsConsole }` : reportLog;
		}
	} catch ( e ) {
		// Continue regardless of error

		// Output error if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// eslint-disable-next-line no-console
			console.warn( e );
		}
	}

	// Escape HTML from report log
	try {
		reportLog = reportLog
						.replace( /&/g, '&amp;' )
						.replace( /</g, '&lt;' )
						.replace( />/g, '&gt;' )
						.replace( /"/g, '&quot;' )
						.replace( /'/g, '&#039;' );
	} catch ( e ){
		// Continue regardless of error

		// Output error if the developer mode is enabled
		if ( UncannyAutomator.dev.developerMode ) {
			// eslint-disable-next-line no-console
			console.warn( e );
		}
	}

	// Create buttons
	const modalButtons = {};

	if ( isDefined( settings.buttons.cancel ) ){
		modalButtons.cancel = settings.buttons.cancel.label;
	}

	if ( isDefined( settings.buttons.confirm ) ){
		modalButtons.confirm = settings.buttons.confirm.label;
	}

	let $modal = new Modal(
		{
			title: `<span class="modal__title-icon"><automator-icon id="exclamation-triangle"></automator-icon></span> ${ settings.title }`,
			warning: `${ settings.warning }`,
			content: `
			<div>
				${ ! isEmpty( settings.content ) ? settings.content : '' }
				<div class="modal__content-code">
					<pre id="uap-debugging">${ reportLog }</pre>
				</div>
			</div>
		`,
			buttons: modalButtons,
		},
		{
			size: 'extra-large',
			warningFirst: true,
		}
	);

	$modal.setEvents( {
		onConfirm: () => {
			settings.buttons.confirm.onClick( $modal );
		},
		onCancel: () => {
			settings.buttons.cancel.onClick( $modal );
		},
		afterSetEvents: () => {
			// Get the element with the debugging
			const $debuggingConsole = $( '#uap-debugging' );

			// On click, copy the content
			$debuggingConsole.on( 'click', () => {
				// Select the content of the container
				// This is just to improve the UX
				const range = document.createRange();
				range.selectNodeContents( $debuggingConsole[ 0 ] );
				// eslint-disable-next-line @wordpress/no-global-get-selection
				window.getSelection().removeAllRanges();
				// eslint-disable-next-line @wordpress/no-global-get-selection
				window.getSelection().addRange( range );
			} );
		},
	} );
};

/**
 * Filters items with specific post meta value
 *
 * @since 3.0
 * @param {array}  items The array of items to filter
 * @param {String} meta  Meta key
 * @param {String} value Meta value
 * @return {array} Array of filtered items
 */
export const filterMetaValue = ( items, meta, value ) => {
	const itemsWithMeta = {};

	$.each( items, ( itemId, item ) => {
		if ( item.meta[ meta ] && item.meta[ meta ] == value ) {
			itemsWithMeta[ itemId ] = item;
		}
	} );

	return itemsWithMeta;
};

/**
 * Filters items without specific post meta
 *
 * @param {Array}  items The array of items to filter
 * @param {String} meta  Meta key
 * @param {String} value Meta value
 * @return {Array}          Array of filtered items
 */
export const filterMeta = ( items, meta, value = true ) => {
	const filteredItems = {};

	$.each( items, ( itemId, item ) => {
		if ( meta in item.meta === value ) {
			filteredItems[ itemId ] = item;
		}
	} );

	return filteredItems;
};

/**
 * Opens a link on a new tab
 *
 * @param {String} href The URL
 * @return {undefined}
 */
export const openInNewTab = ( href ) => {
	Object.assign( document.createElement( 'a' ), {
		target: '_blank',
		href: href,
	} ).click();
};

/**
 * Adds the UTM parameters to a given URL
 *
 * @param {String} url                 The URL
 * @param {Object} parameters          The parameters to add
 * @param {String} parameters.medium   The utm_medium
 * @param {String} parameters.campaign The utm_campaign
 * @param {String} parameters.content  The utm_content
 * @return {String}                     The URL with the parameters
 */
export const addUTMParameters = ( url, { medium, campaign, content } ) => {
	// Define the parameters we're going to add
	const urlParameters = {};

	// Add the utm_source
	// eslint-disable-next-line camelcase
	urlParameters.utm_source =
		isDefined( UncannyAutomator.wp ) && UncannyAutomator.wp ? 'uncanny_automator_pro' : 'uncanny_automator';

	// Add the medium
	if ( ! isEmpty( medium ) ) {
		// eslint-disable-next-line camelcase
		urlParameters.utm_medium = medium;
	}

	// Add the campaign
	if ( ! isEmpty( campaign ) ) {
		// eslint-disable-next-line camelcase
		urlParameters.utm_campaign = campaign;
	}

	// Add the content
	if ( ! isEmpty( content ) ) {
		// eslint-disable-next-line camelcase
		urlParameters.utm_content = content;
	}

	// Check if we have to add the utm_r parameter
	if ( ! isEmpty( UncannyAutomator.marketing.utmR ) ) {
		// eslint-disable-next-line camelcase
		urlParameters.utm_r = UncannyAutomator.marketing.utmR;
	}

	// Add the parameters
	url = addParametersToURL( url, urlParameters );

	// Return the modified URL
	return url;
};
