// eslint-disable-next-line import/no-unresolved
import $ from 'jquery';

import { addParametersToURL, isDefined } from '../common/utilities.js';

/**
 * Creates circle on the bottom right corner for users to send feedback
 */
export default class SendFeedback {
	// eslint-disable-next-line require-jsdoc
	constructor() {
		this.render();
	}

	/**
	 * Creates the elements
	 *
	 * @return {undefined}
	 */
	render() {
		const $container = $( `
			<div class="uap-report-bug">
				<a
					class="uap-report-bug__btn"

					uap-tooltip="${ UncannyAutomator.i18n.sendFeedback.title }"
					uap-tooltip-flow="left"

					target="_blank"
					href="${ addParametersToURL( 'https://automatorplugin.com/feedback/', this.utmBtnParameters ) }"
				>
					<span class="uap-report-bug__icon"></span>
				</a>
			</div>
		` );

		// Append to the body
		$( 'body' ).append( $container );
	}

	/**
	 * Returns the UTM parameters for the link that goes
	 * to automatorplugin.com for users to leave feedback
	 *
	 * @return {Object} UTM parameters
	 */
	get utmBtnParameters() {
		// Check version
		let version, utmSource;

		if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ) {
			version = `Uncanny Automator ${ UncannyAutomator.version }`;
			utmSource = 'uncanny_automator';
		} else {
			version = `Uncanny Automator Pro ${ UncannyAutomator.version }`;
			utmSource = 'uncanny_automator_pro';
		}

		return {
			version: version,
			// eslint-disable-next-line camelcase
			utm_source: utmSource,
			// eslint-disable-next-line camelcase
			utm_medium: 'send_feedback',
			// eslint-disable-next-line camelcase
			utm_content: 'bulb_icon',
		};
	}
}
