import $ from 'jquery';

import {
	itemsObject,
	getDOMElements,
	isEmpty,
	isDefined,
	filterMeta,
	getURLParameter,
	filterMetaValue
}                               from '../common/utilities.js';

import UserSelector             from '../user-selector/user-selector.js';

import MetaboxItems             from '../metabox/items.js';
import MetaboxFooter            from '../metabox/footer.js';

import ItemInitialAction        from './item-initial-action.js';
import Closures                 from './closures.js';
import ItemCreatorIntegrations  from './integrations-grid.js';

import RenderConditionsInActions from '@builder/item/button-conditions/render-conditions-in-actions';

export default class Actions {

	/**
	 * Renders all the actions in the UI
	 */

	constructor(){
	   
		// Debugging: Create console group
		// console.group( 'Actions' );

		// Get and clear metabox
		let $metabox = getDOMElements( 'metabox', { type: 'action' });
		$metabox.empty();

		// Check if we have to create the User Selector
		if ( UncannyAutomator.recipe.type == 'anonymous' ){
			// Create User Selector
			new UserSelector();
		}

		// Get recipe actions
		let actions = itemsObject( 'get', {
			filter: {
				itemType: 'action',
			}
		});

		// Check if it has actions
		if ( ! isEmpty( actions ) && isDefined( UncannyAutomator.wp ) && UncannyAutomator.wp ){

			let instantActions = filterMeta( actions, 'async_mode', false );
			let delayedActions = filterMetaValue( actions, 'async_mode', 'delay' );
			let scheduledActions = filterMetaValue( actions, 'async_mode', 'schedule' );

			if ( ! isEmpty( delayedActions ) || ! isEmpty( scheduledActions ) ) {
				if ( ! isEmpty( instantActions ) ) {
					// Render non async actions
					this.metaboxItems = new MetaboxItems( 'action', instantActions, true );
				}
				   
				// Add Closures
				// Add action button
				new MetaboxFooter( 'action' );

				// Check if we have to show the integrations on load
				if ( this.isContinuingIntegrationConnection() ){
					// Render the integrations list
					new ItemCreatorIntegrations( 'action' );
				}
				
				new Closures( UncannyAutomator.i18n.action.asyncActions.closuresLabelAsyncOnly );
				
				 // Render async actions
				this.metaboxItems = new MetaboxItems( 'action', delayedActions, false );
				this.metaboxItems = new MetaboxItems( 'action', scheduledActions, false );
			}
			else {
				// Render non-async actions
				this.metaboxItems = new MetaboxItems( 'action', instantActions, true );

				// Add action button
				new MetaboxFooter( 'action' );

				// Check if we have to show the integrations on load
				if ( this.isContinuingIntegrationConnection() ){
					// Render the integrations list
					new ItemCreatorIntegrations( 'action' );
				}

				// Add closures
				new Closures( UncannyAutomator.i18n.action.asyncActions.closuresLabelAsyncOnly );
			}
		} 
		else if ( ! isEmpty( actions ) ) {
			this.metaboxItems = new MetaboxItems( 'action', actions, true );

			// Add action button
			new MetaboxFooter( 'action' );

			// Check if we have to show the integrations on load
			if ( this.isContinuingIntegrationConnection() ){
				// Render the integrations list
				new ItemCreatorIntegrations( 'action' );
			}

			// Add closures
			new Closures( UncannyAutomator.i18n.action.asyncActions.closuresLabelAsyncOnly );
		}
		else {
			// Render the first step of the item creator
			new ItemInitialAction();

			// Check if we have to show the integrations on load
			if ( this.isContinuingIntegrationConnection() ){
				// Render the integrations list
				new ItemCreatorIntegrations( 'action' );
			}
			
			// Add closures
			new Closures( UncannyAutomator.i18n.action.asyncActions.closuresLabelAsyncOnly );
		}

		new RenderConditionsInActions();

		// Try to make the actions sortable
		try {
			this.metaboxItems.makeSortable();
		} catch ( e ){}

		// Debugging: Close console group
		// console.groupEnd();
	}

	isContinuingIntegrationConnection(){
		return getURLParameter( null, 'ua_connecting_integration' ) == 1 && getURLParameter( null, 'ua_connecting_integration_context' ) == 'action';
	}
}