
class ItemReorder {
	constructor(){
		return this.createDragHandle();
	}

	createDragHandle(){

		// Create elements
		this.handle = document.createElement( 'div' );
		this.handle.setAttribute( 'class', 'item-handle' );

		this.handleIcon = document.createElement( 'automator-icon' );
		this.handleIcon.setAttribute( 'id', 'sort-circle' );

		this.handle.append( this.handleIcon );

		return this.handle;
	}
}

export default ItemReorder;