import $ from 'jquery';

// Import sprintf-js
const vsprintf = require( 'sprintf-js' ).vsprintf;

import {
	getItemsByIntegration,
	getDOMElements,
	restCall,
	isDefined,
	isEmpty,
	replaceCurlyBraces,
	addParametersToURL,
	addUTMParameters,
	openInNewTab,
	recipeSetRequiresUser
} from '../common/utilities.js';

import Modal          from '../common/modal.js';

import ItemCreatorIntegrations from './integrations-grid.js';
import ItemHeaderMenu          from './action-menu.js';
import ItemHeaderIcon          from './integration-icon.js';
import ItemHeaderTag           from './integration-name.js';
import Triggers 	           from './triggers.js';
import Actions 		           from './actions.js';

class ItemCreatorTaskSelector {
	constructor( itemType, integrationID, $currentItem ){
		this.itemType 		= itemType;
		this.integrationID 	= integrationID;
		this.$currentItem 	= $currentItem;

		// By this step, remove the URL parameters added
		// while connecting a premium integration
		this.removeConnectingIntegrationsParameters();

		// Render DOM elements
		this.render();
	}

	render(){
		// Get content
		const content = {
			// The section title. Either "Select a trigger" or "Select an action"
			title: this.itemType == 'trigger' ? UncannyAutomator.i18n.trigger.select : UncannyAutomator.i18n.action.select,

			// Icon. The integration icon
			$icon: new ItemHeaderIcon( this.integrationID ),

			// Integration name tag
			$integrationNameTag: new ItemHeaderTag( {
				integrationId: this.integrationID,
			} ),

			// The action menu. The three dots on the upper right corner.
			$actionMenu: new ItemHeaderMenu( this.itemType )
		}

		// Create template
		const $template = document.createElement( 'div' );
		$template.classList.add( 'item', 'uap-item' );

		// Add data useful for the JS
		$template.setAttribute( 'data-integration', this.integrationID );

		// Save it as a property
		this.$item = $template;

		// Add content
		$template.insertAdjacentHTML( 'beforeEnd', `
			<div class="uap-item-inner">
				<div class="uap-item-sidebar"></div>
				<div class="uap-item-top-header">
					<div class="uap-item-top-header-left"></div>
					<div class="uap-item-top-header-right"></div>
				</div>
				<div class="uap-item-inner-content"></div>
			</div>
		` );

		// Icon. The integration icon
		$template.querySelector( '.uap-item-sidebar' )
			.appendChild( content.$icon );

		// Add integration name tag
		$template.querySelector( '.uap-item-top-header-left' )
			.appendChild( content.$integrationNameTag );

		// Add action menu
		$template.querySelector( '.uap-item-top-header-right' )
			.appendChild( content.$actionMenu.get(0) );

		// Add inner content
		$template.querySelector( '.uap-item-inner-content' )
			.appendChild( this.createItemsList().get(0) );

		// Replace the current item with the new content
		this.$currentItem
			.replaceWith( $template );
	}

	createItemsList(){
		let items, searchPlaceholder, unlockItems;

		// Get data depending on the item type
		if ( this.itemType == 'trigger' ){
			items 	           = getItemsByIntegration( this.integrationID, 'triggers' );
			searchPlaceholder  = UncannyAutomator.i18n.trigger.search;
			unlockItems        = UncannyAutomator.i18n.proOnly.unlockTriggers;
		}
		else if ( this.itemType == 'action' ){
			items              = getItemsByIntegration( this.integrationID, 'actions' );
			searchPlaceholder  = UncannyAutomator.i18n.action.search;
			unlockItems        = UncannyAutomator.i18n.proOnly.unlockActions;
		}

		// Create the main container
		const $container   = $( `
			<div class="item-options item-options--nopadding item-options--noborder">
				<div class="form-element">
					<div class="item__task-selector">
						<div class="item-task-selector__search">
							<input
								type="text"
								class="item-task-selector__search-input"
								placeholder="${ searchPlaceholder }"
							>
						</div>
						<div class="item-task-selector__options">
							<div class="item-task-selector-options__group item-task-selector-options__group--available"></div>
							<div class="item-task-selector-options__group item-task-selector-options__group--unavailable">
								<div class="item-task-selector-options__group-items"></div>
							</div>
							<div class="item-task-selector__option item-task-selector__option--no-results item-task-selector__option--hide">
								${ UncannyAutomator.i18n.noResults }
							</div>
						</div>
					</div>
				</div>
			</div>
		` );

		// Get important elements
		const $optionsContainer = $container.find( '.item-options' ),
			  $taskSelector     = $container.find( '.item__task-selector' ),
			  $searchField      = $container.find( '.item-task-selector__search-input' ),
			  $itemsList        = {
				container: $container.find( '.item-task-selector__options' ),
				available: $container.find( '.item-task-selector-options__group--available' ),
				unavailable: {
					container: $container.find( '.item-task-selector-options__group--unavailable' ),
					list:      $container.find( '.item-task-selector-options__group-items' )
				},
				noResults: $container.find( '.item-task-selector__option--no-results' )
			  };

		// Check if it has available items
		if ( items.hasAvailableItems ){
			// Sort available items
			// Move the ones that require user data to the bottom if the user has Automator free
			// and this is an anonymous recipe
			if (
				// Check if WP is ready
				( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp )
				// Check if the recipe is anonymous
				&& UncannyAutomator.recipe.type == 'anonymous'
			){
				const itemsThatRequireUserData     = [],
					  itemsThatDontRequireUserData = [];

				items.available.forEach(( item ) => {
					if ( item.requires_user ){
						itemsThatRequireUserData.push( item );
					}
					else {
						itemsThatDontRequireUserData.push( item );
					}
				});

				items.available = [ ...itemsThatDontRequireUserData, ...itemsThatRequireUserData ];
			}

			// Append available items to the list container
			$itemsList.available.append( items.available.map(( item ) => {
				// Check if it's a Pro item
				const isPro = item.is_pro;

				// Check if the user can't add this action to this recipe
				// This happens if the action requires user data, but the user has Automator free
				const cannotAddRequiresUserData = (
					// Check if WP is ready
					( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp )
					// Check if the recipe is anonymous
					&& UncannyAutomator.recipe.type == 'anonymous'
					// Check if the action requires user data
					&& item.requires_user
				);

				// Create container
				const $item = $( `
					<div class="item-task-selector__option ${ isPro ? 'item-task-selector__option--pro' : '' } ${ item.requires_user ? 'item-task-selector__option--requires-user' : '' } ${ cannotAddRequiresUserData ? 'item-task-selector__option-cannot-add' : '' }"
						data-search="${ item.select_option_name.toLowerCase().replace( /\s+|{|}/g, '' ) }">
						<div class="item-task-selector__option-sentence">
							${ cannotAddRequiresUserData ? '<automator-icon id="lock"></automator-icon>' : '' }
							${ replaceCurlyBraces( item.select_option_name, '<span class="item-task-selector__option-variable">', '</span>' ) } ${ isPro ? `<span class="item-task-selector-option__badge">${ UncannyAutomator.i18n.proOnly.pro }</span>` : '' }
						</div>
						<div class="item-task-selector__option-properties">
							${ UncannyAutomator.recipe.type == 'anonymous' && ! UncannyAutomator.recipe.requiresUserData && item.requires_user ? `<span class="item-task-selector-option__requires-user">${ UncannyAutomator.i18n.needsUserData }</span>` : '' }
						</div>
					</div>
				` );

				// Listen clicks on the container
				$item.on( 'click', () => {
					// Add loading class to the parent container
					$taskSelector.addClass( 'item__task-selector--loading' );

					// Add loading class to the container
					$item.addClass( 'item-task-selector__option--loading' );

					// Get the correct request code
					const restRequestCode = [ 'trigger', 'action' ].includes( this.itemType ) ? `add-new-${ this.itemType }` : '';
					
					// Save the value of the fields with default values
					// in the item meta
					let defaultMeta = {}
					$.each( item.options, ( index, option ) => {
						if ( isDefined( option.default_value ) ){
							defaultMeta[ option.option_code ] = option.default_value;
						}
					});

					// Check if the trigger is an anonymous trigger
					if ( item.type == 'anonymous' ){
						// Save the "can_log_in_new_user" value
						defaultMeta.can_log_in_new_user = isDefined( item.can_log_in_new_user ) ? item.can_log_in_new_user : true;
					}

					// Do REST call to add the item
					const addItemREST = ( onSuccess, onFail ) => {
						restCall( 'add', {
							recipePostID: UncannyAutomator.recipe.id,
							action:       restRequestCode,
							item_code:    item.code,
							default_meta: $.extend( {}, defaultMeta, {
								integration_name: UncannyAutomator.integrations[ this.integrationID ].name,
								sentence: 		  item.sentence
							})
						}, ( response ) => {
							// Render both metaboxes again
							// This will close the options panel if it's opened
							new Triggers();
							new Actions();

							/*
							if ( this.itemType == 'trigger' ){
								new Triggers();

								// Check if it's an anonymous recipe
								if ( UncannyAutomator.recipe.type == 'anonymous' ){
									// Render the Actions metabox again
									// We're doing this because we need to update the user selector
									new Actions();
								}
							}
							
							if ( this.itemType == 'action' ){
								new Actions();
							}
							*/

							// Check if the ID of the new item is defined
							if ( isDefined( response.post_ID ) ){
								// Get item form element
								const $newItem = getDOMElements( 'item', {
									id: response.post_ID
								});

								// Determine if the item is already valid
								// This will be true only if the new item doesn't have
								// options, or all the options have a default value
								const isValid = UncannyAutomator.recipe.items[ response.post_ID ].isValid;

								// Check if it's valid
								if ( isValid ){
									// Check if the recipe is NOT live
									if ( ! UncannyAutomator.recipe.isLive ){
										// Set the item to live
										$newItem.item
											.find( '.item-status input[type="checkbox"]' )
											.prop( 'checked', true )
											.trigger( 'change' );
									}
								}
								else {
									// Get first token and trigger a click
									// This will open the first group of options
									$newItem.item
										.find( '.item-title__token:not(.item-title__token--filled)' )
										.first()
										.trigger( 'click' );
								}
							}

							// Check if the user defined a custom callback
							if ( isDefined( onSuccess ) ){
								onSuccess( response );
							}
						}, ( response ) => {
							console.log( 'ERROR-TODO. Couldn\'t add the item' );

							// Remove loading class to the parent container
							$taskSelector.removeClass( 'item__task-selector--loading' );

							// Remove loading class to the container
							$item.removeClass( 'item-task-selector__option--loading' );

							// Check if it's an invalid action
							if ( response.success == false ){
								new Triggers();
							}

							// Check if the user defined a custom callback
							if ( isDefined( onFail ) ){
								onFail( response );
							}
						});
					}
						
					// Check if we have to show a modal to set a user before adding the item
					if ( 
						// Check if the item that the user wants to add is an action
						this.itemType == 'action'
						// Check if the recipe is anonymous
						&& UncannyAutomator.recipe.type == 'anonymous' 
						// Check if the recipe currently DOES NOT need user data
						// Otherwise, don't show the modal as the user already knows
						// the recipe needs user data
						&& ! UncannyAutomator.recipe.requiresUserData
						// Check if the action the user is trying to add requires user data
						&& item.requires_user
					){
						// Create modal
						let modal;

						// Remove loading class to the parent container
						$taskSelector.removeClass( 'item__task-selector--loading' );

						// Remove loading class to the container
						$item.removeClass( 'item-task-selector__option--loading' );

						// Check if WP is ready
						if ( ! isDefined( UncannyAutomator.wp ) || ! UncannyAutomator.wp ){
							modal = new Modal({
								title:   `${ UncannyAutomator.i18n.userSelector.userDataModal.action.forbidden.title } <span class="needs-user-data-modal-pro-badge">${ UncannyAutomator.i18n.proOnly.pro }</span>`,
								content: `
									<div class="needs-user-data-modal">
										<div class="needs-user-data-modal__title">${ UncannyAutomator.i18n.userSelector.userDataModal.action.forbidden.content.mustRunOnUser }</div>
										<div class="needs-user-data-modal__sentence">
											${ replaceCurlyBraces( item.select_option_name, '<span>', '</span>' ) }
										</div>
										<div class="needs-user-data-modal__disclaimer">
											<p>
												<div class="description">${ vsprintf( UncannyAutomator.i18n.userSelector.userDataModal.action.forbidden.content.description, [ UncannyAutomator.i18n.uncannyAutomatorPro ] ) } <a href="${ addUTMParameters( 'https://automatorplugin.com/pricing/', { 'medium': 'select_an_item', 'campaign': 'update_to_pro', 'content': 'requires_user_data',
											}) }" target="_blank">${ UncannyAutomator.i18n.upgradeNow } <automator-icon id="external-link"></automator-icon></a></div>
											</p>
											<p>
												<div class="description">${ UncannyAutomator.i18n.userSelector.userDataModal.action.forbidden.content.upgradeLater } <a href="${ addUTMParameters( 'https://automatorplugin.com/knowledge-base/creating-a-recipe/#Recipe_types', { 'medium': 'select_an_item', 'content': 'requires_user_data-learn-more',
											}) }" target="_blank">${ UncannyAutomator.i18n.learnMore } <automator-icon id="external-link"></automator-icon></a></div>
											</p>
										</div>
									</div>
								`,
								buttons: {
									cancel:  UncannyAutomator.i18n.cancel,
									confirm: UncannyAutomator.i18n.userSelector.userDataModal.action.forbidden.confirmButton,
								},
							}, {
								size: 'large'
							});

							modal.setEvents({
								onConfirm: () => {
									// Close and destroy modal
									modal.destroy();

									// Open link in new tab
									openInNewTab( addUTMParameters( 'https://automatorplugin.com/pricing/', { 'medium': 'select_an_item', 'campaign': 'update_to_pro', 'content': 'requires_user_data',
											}) );
								},
								onCancel: () => {
									// Close and destroy modal
									modal.destroy();
								},
							});
						}
						else {
							// Check if we should set the recipe to draft when the user adds the action
							// We're doing this because the user will need to fill the User Selector
							const actionWillSetRecipeToDraft = UncannyAutomator.recipe.isLive;

							modal = new Modal({
								title:   `${ UncannyAutomator.i18n.userSelector.userDataModal.action.allowed.title } <span class="needs-user-data-modal-pro-badge needs-user-data-modal-pro-badge--gray">${ UncannyAutomator.i18n.proOnly.pro }</span>`,
								content: `
									<div class="needs-user-data-modal">
										<div class="needs-user-data-modal__title">${ UncannyAutomator.i18n.userSelector.userDataModal.action.allowed.content.mustRunOnUser }</div>
										<div class="needs-user-data-modal__sentence">
											${ replaceCurlyBraces( item.select_option_name, '<span>', '</span>' ) }
										</div>
										<div class="needs-user-data-modal__disclaimer">
											<div class="description">${ UncannyAutomator.i18n.userSelector.userDataModal.action.allowed.content.description }</div>
										</div>
									</div>
								`,
								buttons: {
									cancel:  UncannyAutomator.i18n.cancel,
									confirm: UncannyAutomator.i18n.userSelector.userDataModal.action.allowed.confirmButton,
								},
							}, {
								size: 'large'
							});

							modal.setEvents({
								onConfirm: () => {
									let modalElements = modal.getElements();

									// Add loading class to button
									modalElements.modal.box.buttons.confirm.addClass( 'uap-btn--loading' ).prop( 'disabled', true );

									// Do an AJAX call to set the global recipe requires user
									recipeSetRequiresUser(
										// Set to true (requires user)
										true,
										// On success
										( response ) => {
											// Do the request
											addItemREST(() => {
												// On success, close and destroy the modal
												modal.destroy();


											}, () => {
												// On fail
												// Remove loading class to button
												modalElements.modal.box.buttons.confirm.removeClass( 'uap-btn--loading' ).prop( 'disabled', false );
											});
										},
										// On fail
										( response ) => {
											// Remove loading class to button
											modalElements.modal.box.buttons.confirm.removeClass( 'uap-btn--loading' ).prop( 'disabled', false );
										}
									);

									// Do a call to set the recipe to draft
									if ( actionWillSetRecipeToDraft ){
										UncannyAutomator.recipe.publish.statusToggle.setValue( false );
									}
								},
								onCancel: () => {
									// Close and destroy modal
									modal.destroy();
								},
							});
						}
					}
					else {
						// Do the request
						addItemREST();
					}
				});

				// Return the
				return $item;
			}));
		}
		else {
			// Remove the available items container
			$itemsList.available.remove();
		}

		// Check if it has unavailable items
		if ( items.hasUnavailableItems ){
			// Append unavailable items to the list container
			$itemsList.unavailable.list.append( items.unavailable.map(( item ) => {

				// Create the UTM medium
				const utm_medium = `unavailable_item-${ this.integrationID }-${ this.integrationID }_${ replaceCurlyBraces( item.name, '', '' ).toLowerCase().replace( /\s+|{|}/g, '_' ) }`;

				// Create container
				const $item = $( `
					<a href="${ addUTMParameters( 'https://automatorplugin.com/pricing/', {
							'medium':   'select_an_item',
							'campaign': 'update_to_pro',
							'content':  utm_medium,
						}) }"
						target="_blank"
						class="item-task-selector__option item-task-selector__option--pro"
						data-search="${ item.name.toLowerCase().replace( /\s+|{|}/g, '' ) }">
						<div class="item-task-selector__option-sentence">
							<automator-icon id="lock"></automator-icon>
							${ replaceCurlyBraces( item.name, '<span class="item-task-selector__option-variable">', '</span>' ) }
							<span class="item-task-selector-option__badge">${ UncannyAutomator.i18n.proOnly.pro }</span>
						</div>
					</a>
				` );

				return $item;
			}));
		}
		else {
			// Remove the unavailable items container
			$itemsList.unavailable.container.remove();
		}

		// Get the list of items
		const $items = $itemsList.container.find( '.item-task-selector__option:not(.item-task-selector__option--no-results)' );

		// Listen changes on the search field
		$searchField.on( 'input', () => {
			// Get the current value
			const query = $searchField.val().toLowerCase().replace( /\s+|{|}/g, '' );

			// Reset before searching
			$items.removeClass( 'item-task-selector__option--show item-task-selector__option--hide' );

			// Create variable to evaluate later if there were results
			let hasResults = true;

			// Check if the query is not empty
			if ( ! isEmpty( query ) ){
				// If value it isn't empty then we have to check if there are going to be results, so false by default
				hasResults = false;

				$.each( $items, ( index, item ) => {
					let $item = $( item ),
						text  = $item.data( 'search' );

					if ( text.search( query ) !== -1 ){
						$item.addClass( 'item-task-selector__option--show' );

						// At least one result, so true
						hasResults = true;
					}
					else {
						$item.addClass( 'item-task-selector__option--hide' );
					}
				});
			}

			// Show and hide noResults
			$itemsList.noResults.removeClass( 'item-task-selector__option--hide item-task-selector__option--show' );

			if ( ! hasResults ){
				$itemsList.noResults.addClass( 'item-task-selector__option--show' );
			}
			else {
				$itemsList.noResults.addClass( 'item-task-selector__option--hide' )
			}
		});

		// Append "Cancel" button
		$optionsContainer.append( this.createCancelButton() );

		return $container;
	}

	createCancelButton(){
		let $cancelButton = $( '<div/>', { 'class': 'uap-btn uap-btn--secondary' }).text( UncannyAutomator.i18n.cancel );

		$cancelButton.on( 'click', () => {
			new ItemCreatorIntegrations( this.itemType, this.$item );
		});

		return $cancelButton;
	}

	removeConnectingIntegrationsParameters(){
		// If the user is seeing this screen it's because the modal is closed,
		// or because the user already connected the integration
		addParametersToURL( window.location.href, {
			ua_connecting_integration:    '',
			ua_connecting_integration_id: '',
			ua_connecting_integration_context: '',
		}, {
			setAsURL: true,
			refresh: false
		});
	}
}

export default ItemCreatorTaskSelector;
