// Import utilities
import { itemsObject } from '@builder/common/utilities';
import recipeHasConditions from '@builder/item/button-conditions/utilities/recipe-has-conditions';

/**
 * Returns an object with the actions with conditions
 *
 * @return {Object} The actions with conditions
 */
const actionsWithoutConditions = () => {
	// Default response
	let actions = {};

	// Get all actions
	const allActions = itemsObject( 'get', {
		filter: {
			itemType: 'action',
		},
	} );

	// Check if the recipe has conditions
	if ( recipeHasConditions() ) {
		// Check which ones have conditions
		Object.entries( allActions ).forEach( ( [ actionId, action ] ) => {
			// Check if it DOESN'T have conditions
			if ( ! action.hasConditions ) {
				// Add action
				actions[ actionId ] = action;
			}
		} );
	} else {
		// Add all actions
		actions = allActions;
	}

	return actions;
};

export default actionsWithoutConditions;
