// eslint-disable-next-line import/no-unresolved
import $ from 'jquery';

import { restCall, isEmpty } from '../common/utilities.js';

class RecipeTypeSelector {
	constructor( doWhenReady ) {
		// Save callbacks
		this.doWhenReady = doWhenReady;

		// Render
		this.render();

		// Get elements
		this.getElements();

		// Listen to the form submission
		this.listenSubmission();
	}

	render() {
		// Create HTML
		const $recipeTypeSelector = $( `
			<div class="uap">
				<div id="uap-type-selector" class="uap-type-selector">
					<div class="uap-type-selector__box">
						<form id="uap-type-selector-form" class="uap-type-selector__form">
							<div class="uap-type-selector__title">
								${ UncannyAutomator.i18n.recipeType.title }
							</div>
							<div class="uap-type-selector__content">
								<div class="uap-type-selector__types">
									<div class="uap-type-selector-type uap-type-selector-type--user">
										<input class="uap-type-selector-type__hidden-field" type="radio" name="uap-recipe-type" value="user" id="uap-recipe-type-user">
										<label class="uap-type-selector-type__label" for="uap-recipe-type-user">
											<div class="uap-type-selector-type__container">
												<div class="uap-type-selector-type__icon">
													<div class="uap-type-selector-type__icon-img uap-type-selector-type__icon-img--user"></div>
												</div>
												<div class="uap-type-selector-type__title">
													${ UncannyAutomator.i18n.recipeType.userRecipeName }
												</div>
												<div class="uap-type-selector-type__description">
													${ UncannyAutomator.i18n.recipeType.userRecipeDescription }
												</div>
											</div>
										</label>
									</div>
									<div class="uap-type-selector-type uap-type-selector-type--anonymous">
										<input class="uap-type-selector-type__hidden-field" type="radio" name="uap-recipe-type" value="anonymous" id="uap-recipe-type-anonymous">
										<label class="uap-type-selector-type__label" for="uap-recipe-type-anonymous">
											<div class="uap-type-selector-type__container">
												<div class="uap-type-selector-type__icon">
													<div class="uap-type-selector-type__icon-img uap-type-selector-type__icon-img--anonymous"></div>
												</div>
												<div class="uap-type-selector-type__title">
													${ UncannyAutomator.i18n.recipeType.everyoneRecipeName }
												</div>
												<div class="uap-type-selector-type__description">
													${ UncannyAutomator.i18n.recipeType.everyoneRecipeDescription }
												</div>
											</div>
										</label>
									</div>
								</div>
							</div>
							<div class="uap-type-selector__warning">
								${ UncannyAutomator.i18n.recipeType.cantChangeLaterNotice }
							</div>
							<div class="uap-type-selector__buttons">
								<button type="submit" id="uap-type-selector-submit-button" class="uap-btn uap-btn--primary">${ UncannyAutomator.i18n.confirm }</button>
							</div>
						</form>
					</div>
				</div>
			</div>
		` );

		// Append container to the body
		$( 'body' ).append( $recipeTypeSelector );
	}

	getElements() {
		// Get elements
		this.$elements = {
			container: $( '#uap-type-selector' ),
			form: $( '#uap-type-selector-form' ),
			warning: $( '#uap-type-selector .uap-type-selector__warning' ),
			submitButton: $( '#uap-type-selector-submit-button' ),
		};
	}

	listenSubmission() {
		// Listen submission
		this.$elements.form.on( 'submit', ( event ) => {
			// Prevent default
			event.preventDefault();

			// Get recipe type
			const recipeType = this.$elements.form.find( 'input[name="uap-recipe-type"]:checked' ).val();

			// Check if it's defined and it's a valid value
			if ( ! isEmpty( recipeType ) ) {
				// Check if it has a valid value
				if ( [ 'user', 'anonymous' ].includes( recipeType ) ) {
					// Save recipe type
					this.saveRecipeType( recipeType );
				} else {
					// The user is trying save a different value
					this.showError( UncannyAutomator.i18n.recipeType.errorTryingToSaveOtherValue );
				}
			} else {
				// The user didn't select an option
				this.showError( UncannyAutomator.i18n.recipeType.errorDidNotSelectType );
			}
		} );
	}

	saveRecipeType( recipeType ) {
		// Define endpoint
		const endpoint = 'change_post_recipe_type';

		// Define data to send
		const data = {
			// eslint-disable-next-line camelcase
			post_ID: UncannyAutomator.recipe.id,
			// eslint-disable-next-line camelcase
			recipe_type: recipeType,
		};

		// Add loading animation to the button
		this.$elements.submitButton.addClass( 'uap-btn--loading uap-btn--disabled' );

		// Do a REST call to save the recipe type
		restCall(
			endpoint,
			data,
			( response ) => {
				// Check if it was successful
				if ( response.success ) {
					// Remove container
					// We should render everything again and
					// add a conditional to not render the elements
					this.$elements.container.remove();

					// Save in main object
					UncannyAutomator.recipe.type = recipeType;

					// Get the container of the title
					const $triggersMetaboxTitle = $( '#uo-recipe-triggers-meta-box-ui h2.hndle.ui-sortable-handle' );

					// Create the title of triggers metabox
					let triggersMetaboxTitle = UncannyAutomator.i18n.trigger.userTriggers;
					if ( UncannyAutomator.recipe.type == 'anonymous' ) {
						triggersMetaboxTitle = UncannyAutomator.i18n.trigger.everyoneTrigger;
					}

					// Change text the title container
					$triggersMetaboxTitle.text( triggersMetaboxTitle );

					// Call callbacks
					this.doWhenReady( recipeType );
				} else {
					// Show error
					this.showError( UncannyAutomator.i18n.recipeType.errorSomethingWentWrong );
				}
			},
			() => {
				// Output if the developer mode is enabled
				if ( UncannyAutomator.dev.developerMode ) {
					// eslint-disable-next-line no-console
					console.error( "Couldn't save the recipe type" );
				}

				// Remove loading class
				this.$elements.submitButton.removeClass( 'uap-btn--loading uap-btn--disabled' );

				// Show error message
				this.showError( UncannyAutomator.i18n.recipeType.errorSomethingWentWrong );
			}
		);
	}

	showError( errorMessage ) {
		// Create error
		const $error = $( `
			<div class="uap-type-selector__error">
				${ errorMessage }
			</div>
		` );

		// Check if the container already has an error container
		const $oldErrorContainer = this.$elements.container.find( '.uap-type-selector__error' );
		if ( $oldErrorContainer.length > 0 ) {
			$oldErrorContainer.remove();
		}

		// Add error container
		$error.insertAfter( this.$elements.warning );

		// Add class to shake
		setTimeout( () => {
			$error.addClass( 'uap-type-selector__error--shake' );
		}, 10 );
	}
}

export default RecipeTypeSelector;
