// Import utilities
import { getDOMElements } from '@builder/common/utilities';

/**
 * Returns the order of the actions.
 * This uses the DOM element to get the order
 *
 * @return {Array} The order of the actions. The elements are IDs
 */
const getActionsOrder = () => {
	// Default response
	const actionsOrder = [];

	// Get the actions metabox
	const $actionsMetabox = getDOMElements( 'metabox', {
		type: 'action',
	} ).get( 0 );

	// Get actions
	const $actions = $actionsMetabox.querySelectorAll( '.uap-item' );

	// Iterate them and get the order
	$actions.forEach( ( $action ) => {
		// Get the action ID
		const actionId = $action.itemId;

		// Push the ID
		actionsOrder.push( actionId );
	} );

	return actionsOrder;
};

export default getActionsOrder;
