// Import utilities
import recipeHasConditions from '@builder/item/button-conditions/utilities/recipe-has-conditions';
import ConditionsBlock from '@builder/item/button-conditions/conditions-block';

/**
 * Renders conditions in the actions metabox
 */
export default class RenderConditionsInActions {
	/**
	 * Checks if the recipe has conditions and renders them
	 *
	 * @return {undefined}
	 */
	constructor() {
		// Check if the recipe has conditions
		if ( recipeHasConditions() ) {
			// Render conditions
			this.render();
		}
	}

	/**
	 * Renders the conditions
	 *
	 * @return {undefined}
	 */
	render() {
		// Iterate conditions
		// eslint-disable-next-line no-unused-vars
		Object.entries( UncannyAutomator.recipe.conditions ).forEach( ( [ conditionsGroupId, conditionsGroup ] ) => {
			// Create block of conditions
			new ConditionsBlock( {
				conditionsGroupId: conditionsGroupId,
			} );
		} );
	}
}
