// Import utilities
import { isDefined } from '@builder/common/utilities';
import getConditionsGroupById from '@builder/item/button-conditions/utilities/get-conditions-group-by-id';

/**
 * Returns the data of a condition using the condition ID
 *
 * @param {Object} options                   The options
 * @param {String} options.conditionsGroupId The unique ID of the conditions group
 * @param {String} options.conditionId       The unique ID of the condition
 * @return {Condition}                       The condition
 */
const getConditionById = ( { conditionsGroupId, conditionId } ) => {
	// Default response
	let condition = {};

	// Get the ConditionsGroup
	const conditionsGroup = getConditionsGroupById( {
		conditionsGroupId: conditionsGroupId,
	} );

	// Check if it's defined
	if ( isDefined( conditionsGroup ) ) {
		// Look for the condition
		if ( conditionsGroup.hasCondition( conditionId ) ) {
			condition = conditionsGroup.getCondition( conditionId );
		}
	}

	return condition;
};

export default getConditionById;
