// eslint-disable-next-line import/no-unresolved
import $ from 'jquery';

import { isDefined, isEmpty } from '../common/utilities.js';

import Field from './index.js';

const sortable = require( 'html5sortable' );

class FieldsRepeater {
	// eslint-disable-next-line no-unused-vars
	constructor( attributes, createDOMelements = true, customValidationFunction = null ) {
		// Save attributes
		this.setAttributes( attributes );

		// Create fields
		this.createFields();
	}

	/**
	 * Set attributes. It add properties like hasValue, hasPlaceholder, etc, automatically
	 *
	 * @since 0.4
	 * @param {object} attributes - The field attributes
	 * @return {object} attributes - The field attributes
	 */

	setAttributes( attributes ) {
		// Has label
		if ( ! isDefined( attributes.hasLabel ) ) {
			if ( ! isEmpty( attributes.label ) ) {
				attributes.hasLabel = true;
			} else {
				attributes.hasLabel = false;
			}
		}

		// Has description
		if ( ! isDefined( attributes.hasDescription ) ) {
			if ( ! isEmpty( attributes.description ) ) {
				attributes.hasDescription = true;
			} else {
				attributes.hasDescription = false;
			}
		}

		// Check if we should sort the rows
		if ( ! isDefined( attributes.canSortRows ) ) {
			attributes.canSortRows = true;
		}

		this.attributes = attributes;
	}

	/**
	 * Create instances of Field
	 *
	 * @since  2.0
	 * @return undefined
	 */

	createFields() {
		// Get fields template
		const rowFieldsTemplate = this.attributes.fields;

		// Create array with fields
		let fieldsRows = [];

		// Check if there is data and create multiple rows of the repeater field
		if ( Array.isArray( this.attributes.currentValue ) && this.attributes.currentValue.length > 0 ) {
			// https://regex101.com/r/tLL7S9/1/
			// Get field id and index from strings like FIELD_1

			// Iterate default rows
			this.attributes.currentValue.forEach( ( row ) => {
				// Clone template row
				let rowFields = JSON.parse( JSON.stringify( rowFieldsTemplate ) );

				// Iterate the rows in the template and try to get their
				// value from the "row" array
				rowFields = rowFields.map( ( field ) => {
					// Check if there is a value for this field
					if ( isDefined( row[ field.optionCode ] ) ) {
						// Save value
						field.currentValue = row[ field.optionCode ];
					} else {
						// Set empty value
						field.currentValue = '';
					}

					// Return field
					return field;
				} );

				// Add row with fields
				fieldsRows.push( rowFields );
			} );
		} else if ( this.attributes.isRequired ) {
			// Otherwise, just create one row
			// Check if the field is required
			//
			// Create an empty row
			const emptyRow = JSON.parse( JSON.stringify( rowFieldsTemplate ) );

			// Remove undefined current value
			emptyRow.currentValue = '';

			// Add it to the main array
			fieldsRows.push( emptyRow );
		}

		// Now iterate the row with fields
		fieldsRows = fieldsRows.map( ( row ) => {
			// Now iterate each group of fields
			row = row.map( ( fieldObject ) => {
				// Remove the label
				fieldObject.label = '';

				// Remove the description
				fieldObject.description = '';

				// Create field
				const field = new Field( fieldObject.fieldType, fieldObject );

				// Return field instance
				return field;
			} );

			// Return row
			return row;
		} );

		// Update the fields property
		this.fieldRows = fieldsRows;
	}

	/**
	 * Create a jQuery DOM element with the field
	 *
	 * @since 0.3
	 */

	createFormElement() {
		// Create response object
		let response = null;

		// Create container
		const $container = $( `<div class="form-repeater"></div>` );

		// Check if it has a label
		if ( this.attributes.hasLabel ) {
			// Create label
			const $label = $( `
				<div class="form-repeater__label">
					${ this.attributes.label }
					${ this.attributes.isRequired ? `<span class="form-repeater__label-required"></span>` : `` }
				</div>
			` );

			// Append label
			$container.append( $label );
		}

		// Create table with the fields
		const $table = this.getFieldsTable();
		$container.append( $table );

		// Add error container
		$table.append( $( `<div class="form-element__error"></div>` ) );

		// Check if it has a description
		if ( this.attributes.hasDescription ) {
			// Create description
			const $description = $( `<div class="form-repeater__description">${ this.attributes.description }</div>` );

			// Append description
			$container.append( $description );
		}

		// Save form element
		this.formElement = $container;
		response = $container;

		// If there is more than one row, make list sortable
		if ( this.fieldRows.length > 1 ) {
			this.makeRowsSortable();
		}

		// Listen save
		this.listenSave();

		// Return form element
		return response;
	}

	/**
	 * Listen save
	 */

	listenSave() {
		// Get form element
		// eslint-disable-next-line no-unused-vars
		this.formElement.on( 'saved', ( event ) => {
			// Update the fields array
			this.updateAttributesFieldsFromRow();
		} );
	}

	/**
	 * Use fieldRows to update the original attributes.fields
	 * We will use this to render the container again after it was
	 * saved
	 */

	updateAttributesFieldsFromRow() {
		// Create the array where we're going to save the fields data
		const fields = [];

		// Iterate fieldRows
		this.fieldRows.forEach( ( rowFields ) => {
			// Create array to save the row fields
			const row = [];

			// Iterate row fields
			rowFields.forEach( ( fieldInstance ) => {
				// Add field
				row.push( fieldInstance.attributes );
			} );

			// Add row
			fields.push( row );
		} );

		// Update fields
		this.attributes.fields = fields;
	}

	/**
	 * Make list of rows sortable
	 */

	makeRowsSortable() {
		// Check if we should make the rows sortable
		if ( this.attributes.canSortRows ) {
			// Make rows sortable
			// Use sortable from the html5sortable library
			sortable( this.formElement.find( '.form-repeater-fields__table-rows' ), {
				forcePlaceholderSize: true,
				handle: '.form-repeater-fields__table-rows-cell--number',
				placeholder: `<tr><td colspan="${ this.fieldRows[ 0 ].length + 2 }">&nbsp;</td></tr>`,
				// eslint-disable-next-line no-unused-vars
				itemSerializer: ( serializedItem, sortableContainer ) => {
					return {
						position: serializedItem.index,
						html: serializedItem.html,
					};
				},
			} );

			// Listen modifications to the rows order
			this.formElement.find( '.form-repeater-fields__table-rows' ).on( 'sortupdate', ( event ) => {
				// Create an array where each element is the index of the row in the old order
				const newOrder = [];

				// Iterate the rows (in their new order)
				$.each( event.detail.destination.items, ( index, row ) => {
					newOrder.push( $( row ).data( 'order' ) );
				} );

				// Change the order of the rows in the FieldRepeater instance
				const newFieldRowsOrder = [];
				newOrder.forEach( ( oldIndex ) => {
					newFieldRowsOrder.push( this.fieldRows[ oldIndex ] );
				} );
				this.fieldRows = newFieldRowsOrder;

				// Render the field agian
				this.reRender();
			} );
		}
	}

	/**
	 * Create and return fields table
	 *
	 * @since  2.0
	 */

	getFieldsTable() {
		// Create variable to save the table
		const $table = $( `
			<div class="form-repeater-fields" data-id="${ this.attributes.optionCode }">
				<table class="form-repeater-fields__table">
					<thead class="form-repeater-fields__table-heading"></thead>
					<tbody class="form-repeater-fields__table-rows"></tbody>
				</table>
				<div class="form-repeater-fields__actions"></div>
			</div>
		` );

		// Create table with the rows
		// Get table headers
		// We will create a table where the name of the column will
		// be the label of the field
		const heading = [];

		// Iterate the original fields object inside attributes (not the Field instances)
		this.attributes.fields.forEach( ( fieldData ) => {
			// Get column title
			const title = isDefined( fieldData.label ) ? fieldData.label : '';

			// Check if the fields under this column are required
			const isRequired = isDefined( fieldData.isRequired ) ? fieldData.isRequired : false;

			// Get the column description
			const description = isDefined( fieldData.description ) ? fieldData.description : '';

			// Add heading column
			heading.push( {
				title: title,
				description: description,
				isRequired: isRequired,
				id: fieldData.optionCode
			} );
		} );

		// Create HTML of the heading
		const $heading = $( `<tr></tr>` );

		// Add the column with for the row number
		$heading.append(
			$(
				`<th class="form-repeater-fields__table-heading-cell form-repeater-fields__table-heading-cell--number">#</th>`
			)
		);

		// Iterate heading columns
		heading.forEach( ( headingColumn ) => {
			// Create column
			const $headingColumn = $( `<th class="form-repeater-fields__table-heading-cell" data-id="${ headingColumn.id }"></th>` );

			// Add the title
			if ( ! isEmpty( headingColumn.title ) ) {
				$headingColumn.append(
					$( `
					<div class="form-repeater-fields__table-heading-cell-title">
						${ headingColumn.title }
						${ headingColumn.isRequired ? '<span class="form-repeater-fields__table-heading-cell-required"></span>' : '' }
					</div>
				` )
				);
			}

			// Add the description
			if ( ! isEmpty( headingColumn.description ) ) {
				$headingColumn.append(
					$( `
					<div class="form-repeater-fields__table-heading-cell-description">
						${ headingColumn.description }
					</div>
				` )
				);
			}

			// Add heading column
			$heading.append( $headingColumn );
		} );

		// Add the column with for the actions
		$heading.append(
			$(
				`<th class="form-repeater-fields__table-heading-cell form-repeater-fields__table-heading-cell--actions"></th>`
			)
		);

		// Append heading
		const $headingContainer = $table.find( '.form-repeater-fields__table-heading' );
		$headingContainer.append( $heading );

		// Create HTML of the rows
		const $rows = $table.find( '.form-repeater-fields__table-rows' );

		// Get number of rows
		const numberOfRows = this.fieldRows.length;

		// Iterate rows of fields
		this.fieldRows.forEach( ( row, index ) => {
			// Create container for the row
			const $row = $( `<tr data-order="${ index }"></tr>` );

			// Add the column with for the row number
			$row.append(
				$(
					`<td class="form-repeater-fields__table-rows-cell form-repeater-fields__table-rows-cell--number">${
						index + 1
					}</td>`
				)
			);

			// Add fields
			row.forEach( ( fieldInstance ) => {
				// Create container
				const $fieldCell = $( `<td class="form-repeater-fields__table-rows-cell" data-id="${ fieldInstance.attributes.optionCode }"></td>` );

				// Get field's DOM element
				const $field = fieldInstance.getFormElement();
				// Make sure all the libraries had been initialized
				fieldInstance.initLibraries();

				// Add field container
				$fieldCell.append( $field );

				// Append field cell to the row
				$row.append( $fieldCell );
			} );

			// Add the column for the actions, and add the delete button
			const $actions = $(
				`<td class="form-repeater-fields__table-rows-cell form-repeater-fields__table-rows-cell--actions"></td>`
			);
			$row.append( $actions );

			const fieldAtts = this.getAttributes();

			// Check if the field requires at least on row and if this is the first row
			if ( index != 0 || ( index == 0 && ( ! this.attributes.isRequired || numberOfRows > 1 ) ) ) {
				// Check if there is a custom "Remove row" label. If there is one,
				// use that, otherwise use the default one
				const removeRowLabel = isDefined( this.attributes.removeRowButtonLabel )
					? this.attributes.removeRowButtonLabel
					: UncannyAutomator.i18n.removeRow;

				// Create delete button
				const $removeRowButton = $(
					`<div class="form-repeater-fields__table-rows-cell-remove" uap-tooltip="${ removeRowLabel }"><span></span></div>`
				);

				// Listen clicks to the remove button
				$removeRowButton.on( 'click', () => {
					// Remove the row by index
					this.deleteRow( index );
				} );

				// if hideActions is false, Add Remove row button
				if ( ! fieldAtts.hideActions ) {
					// Add remove button to the actions cell
					$actions.append( $removeRowButton );
				}
			}

			// Add $row to the $rows element
			$rows.append( $row );
		} );

		// Create "Add row" button
		const fieldAtts = this.getAttributes();
		// if hideActions is false, Add Pair button
		if ( ! fieldAtts.hideActions ) {
			const $addRowButton = $( `
				<div class="uap-btn uap-btn--primary uap-btn--small">
					${ UncannyAutomator.i18n.addRow }
				</div>
			` );

			// Check if we have to change label of the button
			if ( isDefined( this.attributes.addRowButtonLabel ) ) {
				$addRowButton.text( this.attributes.addRowButtonLabel );
			}

			// Listen clicks
			$addRowButton.on( 'click', () => {
				// Show loading animation
				$addRowButton.addClass( 'uap-btn--loading' );

				// Add another row
				this.addRow();
			} );

			// Add actions
			const $actionsContainer = $table.find( '.form-repeater-fields__actions' );
			$actionsContainer.append( $addRowButton );
		}
		return $table;
	}

	/**
	 * Get value
	 *
	 * @since  2.0
	 * @return {object} Object where the key is the field ID and the value is the field's value
	 */

	getValue() {
		// Create object where we're going to save the values
		const value = [];

		// Iterate rows
		this.fieldRows.forEach( ( row ) => {
			// Get the value of the fields
			const fieldsValue = {};

			// Iterate fields
			row.forEach( ( fieldInstance ) => {
				// Add it to the fieldsValue object
				fieldsValue[ fieldInstance.getFieldID() ] = fieldInstance.getValue();
			} );

			// Add fields value to the main array
			value.push( fieldsValue );
		} );

		// Return value
		return value;
	}

	/**
	 * Add row
	 *
	 * @since  2.0
	 */

	addRow( fieldsData = {}, reRender = true ) {
		// Create an empty row
		let emptyRow = JSON.parse( JSON.stringify( this.attributes.fields ) );

		// Remove undefined current value
		emptyRow.currentValue = '';

		// Now iterate the fields
		emptyRow = emptyRow.map( ( fieldObject ) => {
			// Remove the label
			fieldObject.label = '';

			// Remove the description
			fieldObject.description = '';

			// Check if there is a default value for this field
			if ( isDefined( fieldsData[ fieldObject.optionCode ] ) ) {
				// And if there is, use that data
				fieldObject.currentValue = fieldsData[ fieldObject.optionCode ];
			}

			// Create field
			const field = new Field( fieldObject.fieldType, fieldObject );

			// Return field instance
			return field;
		} );

		// Add it to the rows of fields
		this.fieldRows.push( emptyRow );

		// Render form element again
		if ( reRender ) {
			this.reRender( true );
		}
	}

	/**
	 * Rerender the field again
	 */

	reRender( focusFirstFieldOfLastRow = false ) {
		// Destroy CodeMirror instances
		this.destroyCodeMirror();
		// Destroy the token selector
		this.destroyTokenSelector();

		// Create the form element again
		const $currentFormElement = this.formElement;
		const $newFormElement = this.createFormElement( false );
		$currentFormElement.replaceWith( $newFormElement );

		// Create the token selector
		this.initTokenSelector();
		// Create the CodeMirror instances
		this.initCodeMirror();

		// Check if we have to focus the first field of the last row
		if ( focusFirstFieldOfLastRow ) {
			// Get the last row
			const lastRow = this.fieldRows[ this.fieldRows.length - 1 ];

			if ( isDefined( lastRow ) ) {
				// Get the first field
				const firstField = lastRow[ 0 ];

				if ( isDefined( firstField ) ) {
					firstField.focus();
				}
			}
		}
	}

	/**
	 * Delete row
	 *
	 * @since  2.0
	 */

	deleteRow( index ) {
		// Check if there is a row with that index
		if ( isDefined( this.fieldRows[ index ] ) ) {
			// Remove element
			this.fieldRows.splice( index, 1 );
		}

		// Render form element again
		this.formElement.replaceWith( this.createFormElement() );
	}

	/**
	 * Get form element
	 *
	 * @since 2.0
	 * @return {object} jQuery object of the field's container
	 */

	getFormElement() {
		// Return form element
		return this.formElement;
	}

	/**
	 * Get attributes
	 *
	 * @since 2.0
	 * @return {object} Field attributes
	 */

	getAttributes() {
		return this.attributes;
	}

	/**
	 * Get field ID
	 *
	 * @since 2.0
	 * @return {string} The field ID
	 */

	getFieldID() {
		return this.attributes.optionCode;
	}

	/**
	 * Validate the fields
	 *
	 * @since 2.0
	 * @param {boolean} renderError - TRUE if you want to render the field errors, FALSE if you only want to return the response object
	 * @return {object} Object where the key is the field ID and the value is a validateField object
	 */

	validate( renderError = true ) {
		// Create validation object
		const validation = {
			isValid: true,
			message: '',
		};

		// Create array to save the validation of the rows
		const rowsValidation = [];

		// Iterate fields
		this.fieldRows.forEach( ( row ) => {
			// Create object to save the row validation
			const rowValidation = {};

			// Iterate each field
			row.forEach( ( fieldInstance ) => {
				// Validate and add response to the validation object
				rowValidation[ fieldInstance.getFieldID() ] = fieldInstance.validate( renderError );

				// Update global validation
				validation.isValid = validation.isValid && rowValidation[ fieldInstance.getFieldID() ].isValid;
			} );

			// Add row validation to the main validation array
			rowsValidation.push( rowValidation );
		} );

		// Return validation object
		return validation;
	}

	initTokenSelector() {
		this.fieldRows.forEach( ( rowFields ) => {
			// Iterate row fields
			rowFields.forEach( ( fieldInstance ) => {
				// Add field
				fieldInstance.initTokenSelector();
			} );
		} );
	}

	destroyTokenSelector() {
		this.fieldRows.forEach( ( rowFields ) => {
			// Iterate row fields
			rowFields.forEach( ( fieldInstance ) => {
				// Add field
				fieldInstance.destroyTokenSelector();
			} );
		} );
	}

	initCodeMirror() {
		this.fieldRows.forEach( ( rowFields ) => {
			// Iterate row fields
			rowFields.forEach( ( fieldInstance ) => {
				// Add field
				fieldInstance.initCodeMirror();
			} );
		} );
	}

	destroyCodeMirror() {
		this.fieldRows.forEach( ( rowFields ) => {
			// Iterate row fields
			rowFields.forEach( ( fieldInstance ) => {
				// Add field
				fieldInstance.destroyCodeMirror();
			} );
		} );
	}
}

export default FieldsRepeater;
