// Import utilities
import { isDefined, isEmpty } from '@builder/common/utilities';
import getUniqueId from '@builder/utilities/get-unique-id';
import Condition from '@builder/item/button-conditions/condition';
import ItemConditionsModalOptions from '@builder/item/button-conditions/modal-options';

/**
 * Creates a conditions group
 */
export default class ConditionsGroup {
	// Private properties
	// Mode. Use .mode
	#_mode = 'any';

	// Actions. Use .actions
	#_actions = [];

	// Conditions. Use .conditions
	#_conditions = {};

	/**
	 * Creates an unique ID and adds the condition group to the main object
	 *
	 * @return {undefined}
	 */
	constructor() {
		// Create unique ID
		this.id = getUniqueId();

		// Add it to the main object
		UncannyAutomator.recipe.conditions[ this.id ] = this;
	}

	/**
	 * Adds a condition
	 *
	 * @param {Object} options                 The options
	 * @param {String} options.integrationCode The unique code of the integration
	 * @param {String} options.conditionCode   The unique code of the condition
	 * @param {Object} options.fields          The fields
	 * @return {Condition}                     The condition
	 */
	addCondition( { integrationCode, conditionCode, fields } ) {
		// Create condition
		const condition = new Condition();

		// Set the integration
		condition.integrationCode = integrationCode;

		// Set the condition code
		condition.conditionCode = conditionCode;

		// Set the fields data
		condition.fields = fields;

		// Add condition
		this.#_conditions[ condition.id ] = condition;

		// Return the new condition
		return condition;
	}

	/**
	 * Removes a condition
	 *
	 * @param {Object} options             The options
	 * @param {String} options.conditionId The unique ID of the condition (NOT the code, the ID)
	 * @return {undefined}
	 */
	removeCondition( { conditionId } ) {
		// Check if the condition exists
		if ( isDefined( this.conditions[ conditionId ] ) ) {
			// Remove it
			delete this.#_conditions[ conditionId ];
		}
	}

	/**
	 * Edits a condition
	 *
	 * @param {Object} options             The options
	 * @param {String} options.conditionId The unique ID of the condition (NOT the code, the ID)
	 * @return {undefined}
	 */
	editCondition( { conditionId } ) {
		// Create modal to edit condition
		new ItemConditionsModalOptions( {
			conditionsGroupId: this.id,
			conditionId: conditionId,
			comingFrom: 'edit-condition-pill',
		} );
	}

	/**
	 * Sets the mode
	 *
	 * @param {String} mode The mode. Either "any" or "all"
	 * @return {undefined}
	 */
	set mode( mode ) {
		// Check if the mode is valid
		if ( [ 'all', 'any' ].includes( mode ) ) {
			this.#_mode = mode;
		} else {
			// Set default mode
			this.#_mode = 'any';
		}
	}

	/**
	 * Returns the mode
	 *
	 * @return {String} The mode
	 */
	get mode() {
		return this.#_mode;
	}

	/**
	 * Adds the actions
	 *
	 * @param {Array} actions The actions IDs
	 * @return {undefined}
	 */
	set actions( actions ) {
		// Default values
		this.#_actions = [];

		try {
			// Iterate the actions
			actions.forEach( ( actionId ) => {
				// Check if the action actually exists
				if ( isDefined( UncannyAutomator.recipe.items[ actionId ] ) ) {
					// Add the action ID
					this.#_actions.push( actionId );
				}
			} );
		} catch ( e ) {
			if ( UncannyAutomator.dev.developerMode ) {
				// eslint-disable-next-line no-console
				console.error( "ConditionsGroup: Couldn't set the actions. Make sure the array is valid." );
			}
		}
	}

	/**
	 * Returns the actions
	 *
	 * @return {Array} The IDs
	 */
	get actions() {
		// Remove actions that no longer exist
		// Iterate actions
		this.#_actions = this.#_actions.filter( ( actionId ) => {
			// Check if the action exists
			return isDefined( UncannyAutomator.recipe.items[ actionId ] );
		} );

		return this.#_actions;
	}

	/**
	 * Sets the conditions
	 *
	 * @param {Array} conditionsList An array with conditions
	 * @return {undefined}
	 */
	set conditions( conditionsList ) {
		// Default value
		this.#_conditions = {};

		try {
			// Iterate conditions
			conditionsList.forEach( ( condition ) => {
				// Add condition
				this.addCondition( {
					integrationCode: condition.integration,
					conditionCode: condition.condition,
					fields: condition.fields,
				} );
			} );
		} catch ( e ) {
			if ( UncannyAutomator.dev.developerMode ) {
				// eslint-disable-next-line no-console
				console.error( "ConditionsGroup: Couldn't set the conditions. Make sure the array is valid." );

				// eslint-disable-next-line no-console
				console.error( e );
			}
		}
	}

	/**
	 * Returns the conditions
	 *
	 * @return {Array} The conditions
	 */
	get conditions() {
		return this.#_conditions;
	}

	/**
	 * Returns an object with sanitazed data for the backend
	 *
	 * @return {Object} Sanitazed data
	 */
	get formattedForMeta() {
		// Default response
		let conditionsGroupForMeta = {};

		/**
		 * The backend expects
		 *
		 * {
		 * 	 actions: [], // Array with actions IDs
		 * 	 mode: 'any', // String, either "all" or "any"
		 * 	 conditions: [] // Array with conditions
		 * }
		 */
		conditionsGroupForMeta = {
			actions: this.actions,
			mode: this.mode,
			conditions: [],
		};

		// Iterate conditions
		// eslint-disable-next-line no-unused-vars
		Object.entries( this.conditions ).forEach( ( [ conditionId, condition ] ) => {
			// Format the condition too
			conditionsGroupForMeta.conditions.push( condition.formattedForMeta );
		} );

		return conditionsGroupForMeta;
	}

	/**
	 * Checks whether the conditions group has an action
	 *
	 * @param {Integer} actionId The action ID
	 * @return {Boolean}           True if it has it
	 */
	hasAction( actionId ) {
		return this.actions.includes( actionId );
	}

	/**
	 * Checks whether this conditions group has a condition
	 *
	 * @param {String} conditionId The unique condition ID (hash)
	 * @return {Boolean}             True if it has it
	 */
	hasCondition( conditionId ) {
		return isDefined( this.conditions[ conditionId ] );
	}

	/**
	 * Returns a condition given an ID
	 *
	 * @param {String} conditionId The unique condition ID (hash)
	 * @return {Condition}          The condition
	 */
	getCondition( conditionId ) {
		return this.conditions[ conditionId ];
	}

	/**
	 * Destroys this ConditionsGroup
	 *
	 * @return {undefined}
	 */
	destroy() {
		delete UncannyAutomator.recipe.conditions[ this.id ];
	}

	/**
	 * Evaluates if the condition has actions
	 *
	 * @return {Boolean} True if it has actions
	 */
	get hasActions() {
		return ! isEmpty( this.actions );
	}

	/**
	 * Evaluates if the condition has conditions
	 *
	 * @return {Boolean} True if it has conditions
	 */
	get hasConditions() {
		return ! isEmpty( this.conditions );
	}

	/**
	 * Returns the number of actions
	 *
	 * @return {Integer} The number of actions
	 */
	get numberOfActions() {
		return Object.keys( this.actions ).length;
	}

	/**
	 * Returns the number of conditions
	 *
	 * @return {Integer} The number of conditions
	 */
	get numberOfConditions() {
		return Object.keys( this.conditions ).length;
	}
}
