// Import utilities
import { isDefined } from '@builder/common/utilities';
import sanitizeFieldAttributes from '@builder/field/utilities/sanitize-attributes';

/**
 * Modifies the UncannyAutomator.integrations object to add the conditions to an integration
 *
 * @param {Object} options                 The options
 * @param {String} options.integrationCode The integration code
 * @return {undefined}
 */
const addConditionsToIntegrations = ( { integrationCode } ) => {
	const conditions = {
		available: {},
		unavailable: [],
	};

	// Check if the conditions are loaded
	if ( isDefined( UncannyAutomator.actionsConditions ) ) {
		// Get available conditions
		// Check if this integration has available conditions
		if ( isDefined( UncannyAutomator.actionsConditions[ integrationCode ] ) ) {
			// Retrieve conditions
			conditions.available = UncannyAutomator.actionsConditions[ integrationCode ];

			// Sanitize field data
			// eslint-disable-next-line no-unused-vars
			Object.entries( conditions.available ).forEach( ( [ conditionCode, conditionData ] ) => {
				// Iterate fields
				if ( isDefined( conditions.available[ conditionCode ].fields ) ) {
					conditions.available[ conditionCode ].fields = conditions.available[ conditionCode ].fields.map(
						( fieldAttributes ) => {
							// Add the item type
							fieldAttributes.itemType = 'action';

							// Sanitize and return the attributes
							return sanitizeFieldAttributes( fieldAttributes );
						}
					);
				}
			} );
		}

		// Get unavailable conditions
		// Check if this integration has unavailable conditions
		if (
			isDefined( UncannyAutomator.proFeatures[ integrationCode ] ) &&
			isDefined( UncannyAutomator.proFeatures[ integrationCode ].conditions )
		) {
			// Retrieve unavailable conditions
			conditions.unavailable = UncannyAutomator.proFeatures[ integrationCode ].conditions;
		}

		// Check if it has unavailable items
		conditions.hasUnavailableItems = conditions.unavailable.length > 0;

		// Now that we have the items, we have to check if all of them are pro (to show the "Pro" badge)
		conditions.allItemsArePro = Object.values( conditions.available ).every( ( condition ) =>
			isDefined( condition.is_pro ? condition.is_pro : false )
		);

		// Check if all items require user data
		conditions.allRequireUserData = Object.values( conditions.available ).every( ( condition ) =>
			isDefined( condition.require_user ? condition.require_user : false )
		);

		// Check if it has available items
		conditions.hasAvailableItems = Object.values( conditions.available ).length > 0;
	}
	
	// Add conditions to the main object
	UncannyAutomator.integrations[ integrationCode ].items.conditions = conditions;
};

export default addConditionsToIntegrations;
